import '../Assets/css/dashboard.css'
import axios from 'axios';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../Hooks/authHooks';
import { DashHowItWork, LastOperation } from '../RoutesSubComponents/dashboard';
import { ArrayChildLoader } from '../GlobalComponents/Loader';

const Dashboard = () => {
    const { getUserInfo, getUserAgent, authedInfo, apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos
    const [lastOperation, setLastOperation] = useState([]);
    const [userMentor, setUserMentor] = useState({})


    useEffect(() => {
        getUserInfo()
        axios.get(baseApi + '/user/transaction', { headers: headerApi })
            .then(res => setLastOperation(res.data))
            .catch(err => console.log(err))

        getUserAgent(setUserMentor)

    }, [])
    return <div className="dashboard">
        <h1>Mon espace client</h1>

        <section className="dWelcome">
            <h2>Bonjour {authedInfo.prenom} </h2>
            <Link to="/Profil/modification">Mettre a jour mon profil</Link>
        </section>

        {
            lastOperation.length > 0 ?
                <ArrayChildLoader dataIsLoad={lastOperation}>
                    <LastOperation props={{ lastOperation }} />
                </ArrayChildLoader>
                : <section className="dLastInfos">
                    <h2>Dernière transactions</h2>
                    <div className='sorryNotFound'>
                        <h3>Desolé , aucune transaction effectuée.</h3>
                    </div>
                </section>
        }

        <DashHowItWork props={{ userFullName: authedInfo.nom + ' ' + authedInfo.prenom, userMentor }} />
    </div>
}



export default Dashboard;