import React, { useEffect, useLayoutEffect, useState } from 'react'
import "./logement.css";
import cities from "../../../zip-codes/cities.json";

function Logement({setSection, setProgress, setData }) {
    var today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1; //January is 0!
  var yyyy = today.getFullYear();
  if (dd < 10) {
    dd = "0" + dd;
  }
  if (mm < 10) {
    mm = "0" + mm;
  }

  today = yyyy + "-" + mm + "-" + dd;
  const [page, setPage] = useState(1);
  const [logement, setLogement] = useState({
    logementUser:"",
    colocation:"",
    locationType:"",
    loyer:"",
    valeurMonBien:"",
    creditImmo:"",
    creditImmoVal:"",
    creditImmoValRest:"",
    codePostal:"",
    demenagement:"",
    demenagementDate:""
  });
  const [codePostalError, setCodePostalError] = useState(false);

  function singleSelect(e, cl) {
    let doc = document.querySelectorAll(cl);
    if (!logement[e.target.id] || logement[e.target.id] !== e.target.innerText) {
      setLogement({ ...logement, [e.target.id]: e.target.innerText });
      for (let i = 0; i < doc.length; i++) {
        doc[i].classList.remove("selected");
      }
      e.target.classList.add("selected");
    } else {
      setLogement({ ...logement, [e.target.id]: "" });
      e.target.classList.remove("selected");
    }
  }

  useLayoutEffect(() => {
    let logementUser = document.querySelectorAll(".logementUser");
    let colocation = document.querySelectorAll(".colocation");
    let locationType = document.querySelectorAll(".locationType");
    let creditImmo = document.querySelectorAll(".creditImmo");
    let demenagement = document.querySelectorAll(".demenagement");

    if (logement.logementUser && logementUser) {
      for (let i = 0; i < logementUser.length; i++) {
        if (logementUser[i].innerText === logement.logementUser)
        logementUser[i].classList.add("selected");
      }
    }
    if (logement.colocation && colocation) {
        for (let i = 0; i < colocation.length; i++) {
          if (colocation[i].innerText === logement.colocation)
          colocation[i].classList.add("selected");
        }
      }
      if (logement.locationType && locationType) {
        for (let i = 0; i < locationType.length; i++) {
          if (locationType[i].innerText === logement.locationType)
          locationType[i].classList.add("selected");
        }
      }
      if (logement.creditImmo && creditImmo) {
        for (let i = 0; i < creditImmo.length; i++) {
          if (creditImmo[i].innerText === logement.creditImmo)
          creditImmo[i].classList.add("selected");
        }
      }
      if (logement.demenagement && demenagement) {
        for (let i = 0; i < demenagement.length; i++) {
          if (demenagement[i].innerText === logement.demenagement)
          demenagement[i].classList.add("selected");
        }
      }
   
  }, [logement, page]);

  const normalizeAgeNumber = (value) => {
    return value.replace(/\D/gi, "") || "";
  };
  // const normalizeDateNumber = (value) => {
  //   if (new Date(value) > new Date(today)) return "";
  //   else return value;
  // };
    useEffect(() => {
        window.scrollTo(0, 160);
      }, [page]);
    return (
        <>
            {page === 1 && (
        <div className="simulateur-nationalite">
          <span>
          Je suis :
          </span>
          <div>
            <div
              className="logementUser"
              onClick={(e) => singleSelect(e, ".logementUser")}
              id="logementUser"
            >
              Locataire
            </div>
            <div
              className="logementUser"
              onClick={(e) => singleSelect(e, ".logementUser")}
              id="logementUser"
            >
              Propriétaire
            </div>
            <div
              className="logementUser"
              onClick={(e) => singleSelect(e, ".logementUser")}
              id="logementUser"
            >
              Hébergé
            </div>
            <div
              className="logementUser"
              onClick={(e) => singleSelect(e, ".logementUser")}
              id="logementUser"
            >
              Sans domicile stable
            </div>

          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
                setSection("mon foyer");
                setProgress(40);
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.logementUser}
              style={{ backgroundColor: logement.logementUser && "#2e74b9" }}
              onClick={() => {
                switch (logement.logementUser) {
                  case "Locataire":
                    setLogement({...logement,valeurMonBien:"",creditImmo:"",creditImmoVal:"",creditImmoValRest:""})
                    setPage(1.1);
                    break;
                    case "Propriétaire":
                        setLogement({...logement,colocation:"",
                        locationType:"",
                        loyer:""})
                    setPage(1.2);
                    break;
                  default:
                    setLogement({...logement,colocation:"",
                    locationType:"",
                    loyer:"",valeurMonBien:"",creditImmo:"",creditImmoVal:"",creditImmoValRest:""})
                    setPage(2);
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
       {page === 1.1 && (
        <div className="simulateur-nationalite">
          <span>
          Je suis en colocation
          </span>
          <div>
            <div
              className="colocation"
              onClick={(e) => singleSelect(e, ".colocation")}
              id="colocation"
            >
              Oui
            </div>
            <div
              className="colocation"
              onClick={(e) => singleSelect(e, ".colocation")}
              id="colocation"
            >
              Non
            </div>
         

          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.colocation}
              style={{ backgroundColor: logement.colocation && "#2e74b9" }}
              onClick={() => {
                 setPage(1.3)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
       {page === 1.3 && (
        <div className="simulateur-nationalite">
          <span>
          Je loue
          </span>
          <div>
            <div
              className="locationType"
              onClick={(e) => singleSelect(e, ".locationType")}
              id="locationType"
            >
              Un logement non-meublé
            </div>
            <div
              className="locationType"
              onClick={(e) => singleSelect(e, ".locationType")}
              id="locationType"
            >
              Un logement meublé ou hôtel
            </div>
            <div
              className="locationType"
              onClick={(e) => singleSelect(e, ".locationType")}
              id="locationType"
            >
              Une résidence universitaire
            </div>
            <div
              className="locationType"
              onClick={(e) => singleSelect(e, ".locationType")}
              id="locationType"
            >
              Une résidence sociale
            </div>
            <div
              className="locationType"
              onClick={(e) => singleSelect(e, ".locationType")}
              id="locationType"
            >
              Foyer de jeunes travailleurs
            </div>
         

          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.locationType}
              style={{ backgroundColor: logement.locationType && "#2e74b9" }}
              onClick={() => {
                 setPage(1.4)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
       {page === 1.4 && (
           <div className="loyer-wrap">
            <span>
            Mon loyer mensuel charges comprises est de
            </span>
        <div className="simulateur-age loyer">
           
        <div>
          <input
            // ref={ageRef}
            value={logement.loyer}
            type="tel"
            autoFocus
            maxLength="4"
            placeholder="0"
            onChange={(e) => {
              e.target.value = normalizeAgeNumber(
                e.target.value
              );
              setLogement({ ...logement, loyer: e.target.value });
            }}
          />
        </div>
        <span>€</span>
      </div>
      <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.loyer}
              style={{ backgroundColor: logement.loyer && "#2e74b9" }}
              onClick={() => {
                 setPage(2)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
      </div>
      )}

{page === 1.2 && (
           <div className="loyer-wrap">
            <span>
            La valeur de mon bien
            </span>
        <div className="simulateur-age loyer bien">
           
        <div>
          <input
            // ref={ageRef}
            value={logement.valeurMonBien}
            type="tel"
            autoFocus
            maxLength="7"
            placeholder="100 000"
            onChange={(e) => {
              e.target.value = normalizeAgeNumber(
                e.target.value
              );
              setLogement({ ...logement, valeurMonBien: e.target.value });
            }}
          />
        </div>
        <span>€</span>
      </div>
      <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.valeurMonBien}
              style={{ backgroundColor: logement.valeurMonBien && "#2e74b9" }}
              onClick={() => {
                 setPage(1.5)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
      </div>
      )}
       {page === 1.5 && (
        <div className="simulateur-nationalite">
          <span>
          Je rembourse un crédit pour ce logement
          </span>
          <div>
            <div
              className="creditImmo"
              onClick={(e) => singleSelect(e, ".creditImmo")}
              id="creditImmo"
            >
              Oui
            </div>
            <div
              className="creditImmo"
              onClick={(e) => singleSelect(e, ".creditImmo")}
              id="creditImmo"
            >
              Non
            </div>
         

          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.creditImmo}
              style={{ backgroundColor: logement.creditImmo && "#2e74b9" }}
              onClick={() => {
                  switch (logement.creditImmo) {
                      case "Oui":
                          setPage(1.6)
                          break;
                  
                      default:
                          setPage(2)
                          break;
                  }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
      {page === 1.6 && (
           <div className="loyer-wrap">
            <span>
            Je rembourse tous les mois
            </span>
        <div className="simulateur-age loyer">
           
        <div>
          <input
            // ref={ageRef}
            value={logement.creditImmoVal}
            type="tel"
            autoFocus
            maxLength="4"
            placeholder="0"
            onChange={(e) => {
              e.target.value = normalizeAgeNumber(
                e.target.value
              );
              setLogement({ ...logement, creditImmoVal: e.target.value });
            }}
          />
        </div>
        <span>€</span>
      </div>
      <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.creditImmoVal}
              style={{ backgroundColor: logement.creditImmoVal && "#2e74b9" }}
              onClick={() => {
                 setPage(1.7)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
      </div>
      )}
      {page === 1.7 && (
           <div className="loyer-wrap">
            <span>
            Total restant à rembourser
            </span>
        <div className="simulateur-age loyer bien">
           
        <div>
          <input
            // ref={ageRef}
            value={logement.creditImmoValRest}
            type="tel"
            autoFocus
            maxLength="7"
            placeholder="0"
            onChange={(e) => {
              e.target.value = normalizeAgeNumber(
                e.target.value
              );
              setLogement({ ...logement, creditImmoValRest: e.target.value });
            }}
          />
        </div>
        <span>€</span>
      </div>
      <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.creditImmoValRest}
              style={{ backgroundColor: logement.creditImmoValRest && "#2e74b9" }}
              onClick={() => {
                 setPage(2)
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
      </div>
      )}

{page === 2 && (
           <div className="loyer-wrap">
            <span>
            Mon code postal :
            </span>
        <div className="simulateur-age loyer bien postal">
           
        <div>
          <input
            // ref={ageRef}
            value={logement.codePostal}
            type="tel"
            autoFocus
            maxLength="5"
            placeholder="75001"
            onChange={(e) => {
                setCodePostalError(false)
              e.target.value = normalizeAgeNumber(
                e.target.value
              );
              setLogement({ ...logement, codePostal: e.target.value });
            }}
          />
        </div>
        {codePostalError&&<span>
        Code postal invalide   
        </span>}
      </div>
      <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(1)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.codePostal}
              style={{ backgroundColor: logement.codePostal && "#2e74b9" }}
              onClick={() => {
                let test = false;
                for (var i = 0; i < cities.length; i++) {
                  if (cities[i].zip_code === logement.codePostal) {
                    test = true;
                    break;
                  }
                }
                if (test) {
                  setCodePostalError(false);
                  setPage(3)
                } else {
                  setCodePostalError(true);
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
      </div>
)}


{page === 3 && (
        <div className="simulateur-nationalite">
          <span>
          Je compte déménager dans les prochaines semaines
          </span>
          <div>
            <div
              className="demenagement"
              onClick={(e) => singleSelect(e, ".demenagement")}
              id="demenagement"
            >
              Oui
            </div>
            <div
              className="demenagement"
              onClick={(e) => singleSelect(e, ".demenagement")}
              id="demenagement"
            >
              Non
            </div>
         

          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
             setPage(2)
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.demenagement}
              style={{ backgroundColor: logement.demenagement && "#2e74b9" }}
              onClick={() => {
                  switch (logement.demenagement) {
                      case "Oui":
                          setPage(3.1)
                          break;
                  
                      default:
                        setSection("mes revenus");
                        setProgress(80);
                setData((data) => {
                  return { ...data, ...logement };
                });
                          break;
                  }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
       {page === 3.1 && (
        <>
          <div className="simulateur-date">
            <span>Date du déménagement
            <span style={{ color: "	#3c3c3c", fontSize: 13, display: "block" }}>
            Une date approximative suffit
            </span>
            </span>
            <div>
              <input
                value={logement.demenagementDate}
                type="date"
                autoFocus
                // max={today}
                onChange={(e) => {
                  // e.target.value = normalizeDateNumber(e.target.value);
                  setLogement({
                    ...logement,
                    demenagementDate: e.target.value,
                  });
                }}
              />
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(3)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!logement.demenagementDate}
              style={{
                backgroundColor:
                  logement.demenagementDate && "#2e74b9",
              }}
              onClick={() => {
                setSection("mes revenus");
                setProgress(80);
                setData((data) => {
                  return { ...data, ...logement };
                });
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </>
      )}

        </>
    )
}

export default Logement
