import { useState } from 'react';
import '../Assets/css/contact.css'
import { contactFields } from '../Raw/fields';
import { handleUserFieldInfoChange } from "../Tools/formFunction"
import { dispatchBtn, dispatchInputOrSelect } from "../Tools/formValidator";


const Contact = () => {
    const formFields = [contactFields.nom_prenom, contactFields.telephone,
    contactFields.mail, contactFields.message]
    const [formValues, setFormValues] = useState({
        nom_prenom: "",
        telephone: "",
        email: "",
        message: ""
    })
    const [errors, setErrors] = useState([])

    const formCond = errors.length === 0 && formValues.nom_prenom != ""
        && formValues.telephone != "" && formValues.email != "" && formValues.message != ""

    const mailSetterInfo = "contact@france-assist.fr?subject=Message de client France-assit" +
        "&body=Nom et prenom du client : " + formValues.nom_prenom +
        "%0D%0AMail du client : " + formValues.email +
        "%0D%0ATelephone portable du client : " + formValues.telephone +
        "%0D%0AMessage du client : %0D%0A " + formValues.message


    return <div className="contact">
        <section className='cOurInfos'>
            <h2>Pour nous contacter</h2>
            <h4>
                Si vous souhaitez en savoir plus sur France Assist, c’est ici !
            </h4>
            <div>
                <p>
                    <i className="mdi mdi-gmail"></i> contact@france-assist.fr
                </p>
                <p>
                    <i className="mdi mdi-map"></i> 36 Avenue General de Gaulle, 69110 Sainte‑Foy‑lès‑Lyon
                </p>
                <p>
                    <i className="mdi mdi-phone"></i> +33 04 65 84 08 48
                </p>
            </div>

        </section>

        <section className='cSendMessageForm'>
            <h2>Dites nous quelque chose !</h2>
            <form>
                {
                    formFields.map((field, index) => <div className="formSegment"
                        key={"contact fied nb" + index}>
                        {
                            dispatchInputOrSelect(formValues, setFormValues,
                                field, handleUserFieldInfoChange,
                                errors, setErrors)
                        }
                    </div>)
                }

                <div className="formBtn">
                    {
                        formCond ? <a href={'mailto:' + mailSetterInfo} target="_top">Envoyez</a> :
                            <button disabled className='btnDisable'>. . .</button>
                    }
                </div>
            </form>
        </section>
    </div>
}


export default Contact;