import { Navigate } from "react-router-dom";
import { useAuth } from "./Hooks/authHooks";


const ConnectedLayout = ({ children }) => {
    return <>
        <div className='dashboardPart'>
            {children}
        </div>
    </>
}
export const RequireToBeAuthed = ({ children }) => {
    const { authed } = useAuth();
    return authed ? children : <Navigate to="/Connexion" replace />
}

export const NotVisibleIfAuthed = ({ children }) => {
    const { authed } = useAuth();
    return authed ? <Navigate to="/Dashboard" replace /> : children;
}

export const NotRequiredAuth = ({ children }) => {
    const { authed } = useAuth();
    return <>
        {authed === false ? children
            : <Navigate to="/Dashboard" />}
    </>
}

export const AnyoneAuth = ({ children }) => {
    const { authed, userType } = useAuth();
    return <>
        {!authed ? children
            : <ConnectedLayout children={children} />}

    </>
}

export const ProfilRequireAuth = ({ children }) => {
    const { authed, authedInfo } = useAuth();

    return authed && authedInfo.other != null ? { children } : <Navigate to="/Authentification/Connexion" replace />
}

export const RequireToBeEnterprise = ({ children }) => {
    const { userType } = useAuth();
    return <>
        {
            userType == 'ENTREPRISE'
                ? children
                : <Navigate to="/Dashboard" />
        }
    </>
}

export const RequireToBeCandidate = ({ children }) => {
    const { userType } = useAuth();
    return <>
        {
            userType == 'CANDIDAT'
                ? children
                : <Navigate to="/Dashboard" />
        }
    </>
}




