import '../Assets/css/auth.css'
import { ResumeDeLaCommande } from '../GlobalComponents/OtherOnWebsite';
import { AuthBanner, AuthForm } from '../RoutesSubComponents/auth';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';

const stripePromise = loadStripe('pk_live_51Jc7k0DA83aSRojOcslcDv24eNXj3cOEx9doSf8oEgyAMg7LhjstuBYIT2WQSO2RaGrjYZpWcm61iT8fs03HqfXY00sIo4gz7d');

const Auth = ({ props }) => {
    const { authType } = props;

    return <div className="userAuth">
        <AuthBanner authType={authType} />
        <Elements stripe={stripePromise}>
            <AuthForm authType={authType} />
        </Elements>
        {
            authType == 'inscription' ?
                <ResumeDeLaCommande props={{ typeCommande: 'inscription' }} /> : ""
        }
    </div>
}

export default Auth;