import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import simulationSomme from "../helper/SimulationMath";
import { Link } from "react-router-dom";
import "../style/simulationResultat.css";
import axios from "axios";
import TrustBox from "../component/TrustBox";
import { useAuth } from '../Hooks/authHooks';

function SimulationResultat() {
  const simulation = useSelector((state) => state.simulation.simulation);
  let { sum, aides, val } = simulationSomme(simulation);
  const { apiInfos } = useAuth();
  const { baseApi, headerApi } = apiInfos;


  useEffect(() => {
    axios.post(baseApi + '/simulation/simulation_send_mail', { simulation }, { headers: headerApi })
  }, [])

  return (
    <div className="simulationResu">
      <div>
        <h2>Mes aides :</h2>
        <div>
          {sum} €<span>/ mois</span>
        </div>
        <div>
          <span>{aides} </span>
          <span>
            aide{aides === 1 ? "" : "s"} disponible{aides === 1 ? "" : "s"}
          </span>
        </div>
        {aides === 0 && (
          <h3>
            D’après nos calculs, vous n’êtes pas éligible à des aides
            financières.
          </h3>
        )}
      </div>

      <div>
        {val.rsa && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Revenu de Solidarité Active</div>
              <div>
                {val.rsa}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.ppa && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Prime d’activité</div>
              <div>
                {val.ppa}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.aah && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Allocation aux Adultes Handicapés</div>
              <div>
                {val.aah}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.aeeh && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Allocation d’Éducation de l’Enfant Handicapé</div>
              <div>
                {val.aeeh}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.mva && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Majoration pour la vie autonome</div>
              <div>
                {val.mva}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.aspa && (
          <div>
            <div>
              <img src="/sources/aides/msa.svg" alt="aides" />
            </div>
            <div>
              <div>Allocation de solidarité aux personnes âgées</div>
              <div>
                {val.aspa}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.css && (
          <div>
            <div>
              <img src="/sources/aides/css.svg" alt="aides" />
            </div>
            <div>
              <div>Complémentaire santé solidaire</div>
              <div>
                {val.css}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.asi && (
          <div>
            <div>
              <img src="/sources/aides/css.svg" alt="aides" />
            </div>
            <div>
              <div>Allocation Supplémentaire d’Invalidité</div>
              <div>
                {val.asi}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.allocation_annuelle_etudiant && (
          <div>
            <div>
              <img
                src="/sources/aides/allocation_annuelle_etudiant.png"
                alt="aides"
              />
            </div>
            <div>
              <div>Allocation étudiant en difficulté</div>
              {/* <div>
                {val.allocation_annuelle_etudiant}€<span> /mois</span>
              </div> */}
            </div>
          </div>
        )}
        {val.asf && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Allocation de soutien familial</div>
              <div>
                {val.asf}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.cf && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Complément familial</div>
              <div>
                {val.cf}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.paje && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Prestation d’accueil du jeune enfant</div>
              <div>
                {val.paje}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.apl && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Aides Personnalisée au Logement</div>
              <div>
                {val.apl}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.cheque_energie && (
          <div>
            <div>
              <img src="/sources/aides/cheque_energie.jpeg" alt="aides" />
            </div>
            <div>
              <div>Chèque énergie</div>
              <div>
                {val.cheque_energie}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.af && (
          <div>
            <div>
              <img src="/sources/aides/caf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Allocations familiales</div>
              <div>
                {val.af}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.apa_domicile && (
          <div>
            <div>
              <img src="/sources/aides/apa.png" alt="aides" />
            </div>
            <div>
              <div>Allocation personnalisée d’autonomie</div>
              <div>
                {val.apa_domicile}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.garantie_jeunes && (
          <div>
            <div>
              <img src="/sources/aides/garantie_jeunes.jpg" alt="aides" />
            </div>
            <div>
              <div>Garantie Jeunes</div>
              <div>
                {val.garantie_jeunes}€<span> /mois</span>
              </div>
            </div>
          </div>
        )}
        {val.cpf && (
          <div>
            <div>
              <img src="/sources/aides/cpf.jpeg" alt="aides" />
            </div>
            <div>
              <div>Compte personnel de formation</div>
              <div>
                {val.cpf === 1000 ? ">" : "<"} {val.cpf}€
              </div>
            </div>
          </div>
        )}
        {val.mj && (
          <div>
            <div>
              <img src="/sources/aides/actionLogement.jpeg" alt="aides" />
            </div>
            <div>
              <div>AIDE MOBILI-JEUNE</div>
            </div>
          </div>
        )}
        {val.lp && (
          <div>
            <div>
              <img src="/sources/aides/actionLogement.jpeg" alt="aides" />
            </div>
            <div>
              <div>AIDE LOCA-PASS</div>
              <div>
                1200€
              </div>
            </div>
          </div>
        )}
        {val.visale && (
          <div>
            <div>
              <img src="/sources/aides/actionLogement.jpeg" alt="aides" />
            </div>
            <div>
              <div>Garantie VISALE</div>
            </div>
          </div>
        )}
      </div>
      <span
        style={{
          color: "	#3c3c3c",
          fontSize: 13,
          display: "block",
          textAlign: "justify",
          maxWidth: 600,
          margin: "10px auto"
        }}
      >
        *Ces résultats sont fondés sur les seules informations que vous avez
        indiquées et ne constituent en aucune façon un engagement de la part des
        organismes cités. Les montants avancés sont arrondis à une dizaine
        d'euros près
      </span>
      <div>
        <Link to="/Souscription">Je souscris</Link>
      </div>

      {/* <div style={{ margin: "20px auto", textAlign: "center" }}>

        <TrustBox />

      </div> */}
    </div>
  );
}

export default SimulationResultat;
