import { useParams } from 'react-router-dom';
import '../Assets/css/otherDashboard.css'
import { AddDocument, DocumentList } from '../RoutesSubComponents/document';
import axios from 'axios';
import { useState, useEffect, useRef } from 'react';
import { useAuth } from '../Hooks/authHooks';

const Documents = () => {
    const { documentAction } = useParams();
    const [typeDoc, setTypeDoc] = useState('ofUser');
    const { getUserInfo, authedInfo, apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos
    const [documentList, setDocumentList] = useState([]);
    const [agent, setAgent] = useState({});


    // const [user] emails/documents
    useEffect(() => {
        getUserInfo()
        if (documentAction == "liste") {
            let apiUrlEnd = '';
            apiUrlEnd = typeDoc == 'ofUser' ? '/emails' : '/documents'
            axios.get(baseApi + '/user' + apiUrlEnd, { headers: headerApi })
                .then(res => setDocumentList(res.data))
                .catch(err => console.log(err))
        } else {
            axios.get(baseApi + '/user/agent', { headers: headerApi })
                .then(res => setAgent(res.data))
                .catch(err => console.log(err))
        }

    }, [typeDoc, documentAction])

    const tabArray = [
        {
            label: 'Mes documents', id: 'ud1', value: 'ofUser'
        },
        {
            label: 'Documents Conseillers', id: 'ud2', value: 'ofAssist'
        }
    ]

    return <div className="fichier">
        <h1>Documents</h1>
        {
            (() => {
                if (documentAction == 'liste') {
                    return <>
                        <h2>Liste de mes documents</h2>
                        <section className='fSelectTab'>
                            {
                                tabArray.map((ta, index) => <label key={"doc tab nb" + index}
                                    htmlFor={ta.id}
                                    className={ta.value === typeDoc ? "fstSelected" : ""} >
                                    {ta.label}
                                    <input type='radio' name="user_doc" id={ta.id}
                                        onClick={(event) => setTypeDoc(ta.value)}
                                        value={ta.value} />
                                </label>)
                            }
                        </section>

                        {
                            documentList.length > 0 ?
                                <DocumentList props={{ documentList }} />
                                : <div className='sorryNotFound'>
                                    <h3>Desolé , aucun document à afficher .</h3>
                                </div>
                        }
                    </>
                } else if (documentAction == 'ajouter') {
                    return <AddDocument props={{ agent, authedInfo, apiInfos }} />
                }
            })()
        }

    </div>
}

export default Documents;