import { Link } from "react-router-dom"
export const SimulationLink = () => {
    return <Link to="/Simulation-gratuite">Simuler gratuitement</Link>
}

export const EndPageSimulationLink = () => {
    return <div className="endPageLink">
        <Link to="/Simulation-gratuite">Simuler Gratuitement</Link>
    </div>
}


export const FormSubBtn = ({ props }) => {
    const { btnLoad, content, className } = props;

    return <>
        {
            btnLoad ?
                <button className={className + ' btnDisable'}>
                    {content + " ..."}
                    <i className='mdi mdi-spin mdi-loading'></i>
                </button>
                :
                <button className={className + ' '}>
                    {content}
                </button>
        }
    </>
}

