export const SubscriptionForm = ({ props }) => {
    const { formFields,
        handleSubmit, handleChange,
        formValues, setFormValues,
        errors, setErrors,
        formBtn, subscriptionPrice } = props;

    const stripeFormCond = errors.length === 0 &&
        formValues.nom !== "" && formValues.prenom !== ""
        && formValues.telephone !== "" && formValues.codepostal !== ""
        && formValues.email !== ""
        && formValues.number != "" && formValues.carte_expiration_année != ""
        && formValues.carte_expiration_mois != "" && formValues.carte_expiration_année != ""
        && formValues.code_derriere_carte
        && formValues.password !== "" && formValues.password === formValues.password_confirmation

    // function handleSubmit(){
    //     alert('paiement form')
    // }
    return <>
        <div className="checkOutTitle">
            <b>Paiement 100% sécurisé</b>
            <SimpleImage props={{ src: "paiement.png", alt: "Piament pic", className: "" }} />
        </div>

        <form className="subscriptionForm" onSubmit={(event) => handleSubmit(event)}>
            {
                formFields.map((field, index) => <div key={'auth field' + index}
                    className={field.size ? "formSegment " + field.size : "formSegment fsFull"} >
                    <label>{field.label}</label>
                    <section >
                        {
                            dispatchInputOrSelect(formValues, setFormValues,
                                field, handleChange,
                                errors, setErrors)
                        }
                    </section>
                    {getError(field.name, errors)}
                </div>)
            }

            <div className="formTotalPrice">
                <p>Total a payer :</p>
                <b>{subscriptionPrice} €</b>
            </div>
            <div className="formBtn">
                {stripeFormCond ? formBtn : <button className="btnDisable" disabled>Inscription...</button>}
            </div>
        </form>
    </>
}