


document.addEventListener('scroll', () => {
    if (window.scrollY > 50) {
        document.querySelector('.gnAuthAndContact').classList.add('disapearOnScroll')
        document.querySelector('.gnCategorys').classList.add('disapearOnScroll')
        document.querySelector('.generalNav').classList.add('shadowOnScroll')

    } else {
        document.querySelector('.gnAuthAndContact').classList.remove('disapearOnScroll')
        document.querySelector('.gnCategorys').classList.remove('disapearOnScroll')
        document.querySelector('.generalNav').classList.add('shadowOnScroll')
    }
    let fixedHomePageBtn = document.querySelector('.simulatorFixedLink')
    if (window.scrollY > 350) {
        // ifExistOnDom(fixedHomePageBtn, fixedHomePageBtn.classList.add('floatBtnVisible'))
    } else {
        // ifExistOnDom(fixedHomePageBtn, fixedHomePageBtn.classList.remove('floatBtnVisible'))
    }

})

function ifExistOnDom(target, action) {
    if (target) {
        action();
    }
}


