import { Link } from "react-router-dom"
import { SimpleImage } from "../img/global"
import { faqs } from "../../Raw/faqs"

export const Footer = () => {

    const usefulLinks = [
        { link: '/', label: "Accueil" },
        { link: '/Simulation-gratuite', label: "Simuler gratuitement" },
        { link: '/Connexion', label: "Se connecter" },
        { link: '/Inscription', label: "S'inscrire" },
        { link: '/Faq/géneral/2', label: "A propos" },
        { link: '/contact', label: "Contact" },
        { link: '/Mention-legales', label: "Mentions légales" },
        { link: '/Politique-de-confidentialite', label: "Politique de confidentialité" },
        { link: '/CGV', label: "CGV" },
    ]

    const contactLinks = [
        { icon: 'mdi-phone', link: '', label: "+33 04 65 84 08 48" },
        { icon: 'mdi-map', link: '', label: "36 Avenue General de Gaulle, 69110 Sainte‑Foy‑lès‑Lyon" },
        { icon: 'mdi-gmail', link: '', label: "contact@france-assist.fr" },
    ]

    const socialLinks = [
        {
            icon: 'mdi-facebook', label: 'Facebook',
            link: "https://www.facebook.com/France-Assist-112422681101187"
        },
        {
            icon: 'mdi-linkedin', label: 'Linkedin',
            link: "https://www.linkedin.com/company/franceassist/?viewAsMember=true"
        },
        {
            icon: 'mdi-youtube', label: 'Youtube',
            link: "https://www.youtube.com/channel/UCMnnS8vlzb24q1aMhzzNdMQ"
        },
        {
            icon: 'mdi-instagram', label: 'Instagram',
            link: "https://instagram.com/france_assist?utm_medium=copy_link"
        },
        {
            icon: 'mdi-twitter', label: 'Twitter',
            link: "https://twitter.com/assist_france?s=08"
        },
    ]


    return <footer className="footer">
        <section className="fLogo">
            <SimpleImage props={{ src: 'logo.png', alt: 'footer logo', classname: '' }} />
        </section>

        <section className="fUsefulInks">
            <b>Liens utiles</b>
            <div>
                {
                    usefulLinks.map((ul, index) => <Link key={"footer link" + index}
                        to={ul.link}>{ul.label}</Link>)
                }
            </div>
        </section>

        <section className="fContact">
            <b>Nous contacter</b>
            <div>
                {
                    contactLinks.map((cl, index) => <p key={"footer link" + index}>
                        <i className={"rounded mdi " + cl.icon}></i>
                        <span>
                            {cl.label}
                        </span>
                    </p>)
                }
            </div>
        </section>


        <section className="ffaqs">
            <b>Questions fréquentes :</b>
            <div>
                {
                    faqs.géneral.map((faq, index) => <Link key={"footer link" + index}
                        to={'/Faq/géneral/' + (index + 1)}>
                        {faq.question}
                    </Link>)
                }
                {
                    faqs.tarifs.map((faq, index) => <Link key={"footer link" + index}
                        to={'/Faq/tarifs/' + (index + 1)}>
                        {faq.question}
                    </Link>)
                }
            </div>
        </section>


        <section className="fSocialLinks">
            <b>Suivez-nous sur :</b>
            <div>
                {
                    socialLinks.map((sl, index) => <a key={"footer link" + index}
                        href={sl.link}>
                        <span>{sl.label} </span>
                        <i className={"mdi " + sl.icon}></i>
                    </a>)
                }
            </div>
        </section>
    </footer>
}