import { useState } from "react"
import { useParams } from "react-router-dom";
import { BooleanChildLoader } from "../GlobalComponents/Loader";
import { useAuth } from "../Hooks/authHooks";
import { allFields } from "../Raw/fields"
import { Form4, UpdateProfilForm } from "../RoutesSubComponents/modifierProfil";

const Profil = () => {
    const { action } = useParams();
    const { authedInfo } = useAuth()
    return <div className="profil">
        {
            (() => {
                if (action === 'modification') {
                    return <>
                        <h1>Modifier votre profil</h1>
                        <section>
                            <BooleanChildLoader dataIsLoad={authedInfo}>
                                <UpdateProfilForm props={{
                                    authedInfo,
                                    formNb: "1",
                                    formTitle: "Modifier vos informations génerales"
                                }} />
                                <UpdateProfilForm props={{
                                    authedInfo,
                                    formNb: "2",
                                    formTitle: "Modifiier votre Civilité"
                                }} />
                                <UpdateProfilForm props={{
                                    authedInfo,
                                    formNb: "3",
                                    formTitle: "Modifier vos contacts"
                                }} />
                                <Form4 props={{
                                    authedInfo,
                                    formNb: "4",
                                }} />
                            </BooleanChildLoader>

                            {/* <UpdateProfilForm props={{ formNb: "4" }} /> */}

                        </section>
                    </>
                }
            })()
        }
    </div>
}

export default Profil;