import { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { IllustrationImage, SimpleImage } from "../GlobalComponents/img/global"
import { handleUserFieldInfoChange } from "../Tools/formFunction"
import { dispatchBtn, dispatchInputOrSelect, getError } from "../Tools/formValidator"
import axios from "axios";
import { SubscriptionForm } from "../GlobalComponents/OtherOnWebsite"
import { allFields } from "../Raw/fields"
import { useAuth } from "../Hooks/authHooks"
import { useStripe } from "@stripe/react-stripe-js";


export const AuthBanner = ({ authType }) => {
    return <>
        {authType == 'inscription' ?
            <section id="authFullBanner">
                <h1>Souscription au service d’accompagnement</h1>
                <p>Réglez 14,90€ de frais d'inscription puis 9,
                    90€ par mois à compter du mois prochain.
                    Abonnement résiliable à tout moment.</p>
            </section>
            : <section className="uaBanner">
                <IllustrationImage props={{
                    src: 'banners/' + authType.toLowerCase()
                        + '.svg', alt: 'Auth image', className: ''
                }} />
            </section>}
    </>
}


export const AuthForm = ({ authType }) => {
    const { apiInfos, setToken, login, notify } = useAuth();
    const stripe = useStripe()
    const { baseApi, headerApi } = apiInfos;

    const authFields = {
        connexion: [allFields.mail, allFields.password],
        inscription: [
            allFields.nom, allFields.prenom,
            allFields.mail,
            allFields.codepostal, allFields.telephone,
            allFields.password, allFields.password_confirmation,
            allFields.codecarte,
            allFields.mois_expiration_carte, allFields.année_expiration_carte,
            allFields.codederrierecarte,
        ],
    }


    const [formValues, setFormValues] = useState({
        nom: "", prenom: "",
        codepostal: "", telephone: "", email: '',
        carte_expiration_mois: "", carte_expiration_année: "",
        number: "", code_derriere_carte: "",
        password: '', password_confirmation: ''
    });

    function dispatchEmptyErrorArray() {
        return authType === 'inscription' ?
            [
                { name: 'password', error: 'Minimum 6 caractères', type: 'charLength' }
            ]
            : []
    }
    useEffect(() => setErrors(dispatchEmptyErrorArray()), [authType])


    const btnMsg = authType == 'inscription' ? <span><i className="mdi mdi-cash-lock"></i> Valider</span> : 'Se connecter'
    const [errors, setErrors] = useState([])
    const [logError, setLogError] = useState([])

    const [formBtn, setFormBtn] = useState(dispatchBtn('simple', btnMsg))
    const loginFormBtnCondition = formValues.email !== "" && formValues.password !== ""


    function handleChange(event) {
        const name = event.target.name;
        const value = event.target.value;

        if (authType == 'connexion') {
            setFormValues({ ...formValues, [name]: value })
        } else {
            handleUserFieldInfoChange(event, formValues, setFormValues, errors, setErrors)
        }
    }


    function saveUserToDatabase(userData) {
        axios.post(baseApi + '/user/register', userData, { headers: headerApi })
            .then(res => {
                notify('success', 'Compte créé avec succes')
                window.setTimeout(() => {
                    window.location = '/Connexion'
                }, 5000)
            })
            .catch(err => notify("Une erreur s'est produite,lors de la creétion de compte ")
            )
        // console.log(userData)
    }
    function checkPaiement(userData, apiResponse) {
        const { latest_invoice } = apiResponse.data.results[0];
        const { payment_intent } = latest_invoice;
        if (payment_intent) {
            console.log(' intent')
            const { client_secret, status } = payment_intent;

            if (status === "requires_action" || status === "requires_payment_method"
            ) {
                console.log('status')

                stripe.confirmCardPayment(client_secret)
                    .then(scRes => {
                        if (scRes.error) {
                            notify('Une erreur est survenue:' + scRes.error.message)
                        } else {
                            saveUserToDatabase(userData)
                        }
                    })
                    .catch(scErr => notify('error', 'Erreur de confimation de carte :'
                        + scErr.error.message))

            } else {
                console.log('not status')
                saveUserToDatabase(userData)
            }
        } else {
            console.log('not intent')
            saveUserToDatabase(userData)
        }
    }
    // ://france-assist.fr
    // localhost:2123
    function handleSubmit(event) {
        setFormBtn(dispatchBtn('disableAndLoad', 'En cours....'))

        event.preventDefault();
        let body = null;
        let apiUrl = baseApi;
        if (authType == 'connexion') {
            apiUrl += '/user/login'
            body = {
                "email": formValues.email,
                "password": formValues.password
            }

        } else {
            apiUrl += '/user/pay'
            body = {
                "nom": formValues.nom, "prenom": formValues.prenom,
                "telephone": formValues.telephone, "email": formValues.email,
                "codepostal": formValues.codepostal,
                "number": formValues.number,
                "exp_month": formValues.carte_expiration_mois,
                "exp_year": formValues.carte_expiration_année,
                "cvc": formValues.code_derriere_carte, "password": formValues.password
            }
        }


        axios.post(apiUrl, body, { headers: headerApi })
            .then(res => {

                if (authType == 'connexion') {
                    let { token } = res.data;
                    login()
                    setToken(token)
                    window.location = '/Dashboard';
                } else {
                    checkPaiement(body, res)
                }
                setFormBtn(dispatchBtn('simple', btnMsg))
            })
            .catch(err => {
                setFormBtn(dispatchBtn('simple', btnMsg))
                if (authType == 'connexion') {
                    setLogError(err.response.data.message)
                } else {
                    notify('error', "Une erreur s\'est produite pendant le paiement : " +
                        err.response.data.message)
                }
            })
    }


    useEffect(() => {
        let formCond = null;
        if (authType === 'connexion') {
            formCond = loginFormBtnCondition;
            formCond ? setFormBtn(dispatchBtn('simple', btnMsg))
                : setFormBtn(dispatchBtn('disable', btnMsg))
        }
    }, [formValues, authType])

    return <section className="uaForm">
        <h2>{authType.toUpperCase()} </h2>
        {
            authType == 'inscription' ?
                <>
                    <SubscriptionForm props={{
                        formFields: authFields['inscription'],
                        handleSubmit, handleChange,
                        formValues, setFormValues,
                        errors, setErrors,
                        formBtn,
                        subscriptionPrice: '14,90'
                    }} />
                </> : <form className="subscriptionForm" method="post"
                    onSubmit={(event) => handleSubmit(event)}>
                    {
                        authFields['connexion'].map((field, index) => <div key={'auth field' + index}
                            className={field.size ? "formSegment " + field.size : "formSegment fsFull"} >
                            <label>{field.label}</label>
                            <section >
                                {
                                    dispatchInputOrSelect(formValues, setFormValues,
                                        field, handleChange,
                                        errors, setErrors)
                                }
                            </section>
                            {getError(field.name, errors)}
                        </div>)
                    }
                    <div className="formBtn">
                        {formBtn}
                    </div>

                </form>
        }
        <div>
            <b className="errorField">{logError}</b>
        </div>
        <div className="uafOtherAuth">
            {
                authType == 'connexion'
                    ? <>
                        <Link to="/password/mail">Mot de passe oublié ?</Link>
                        <Link to="/Inscription">Vous n'avez pas de compte ! Inscrivez vous .</Link>
                    </>
                    : <Link to="/Connexion">Vous avez un compte ! Connectez vous .</Link>
            }
        </div>
    </section>
}