import axios from "axios"
import { useEffect, useState } from "react"
import { FormSubBtn } from "../GlobalComponents/Btn"
import { useAuth } from "../Hooks/authHooks"
import { allFields } from "../Raw/fields"
import { handleUserFieldInfoChange } from "../Tools/formFunction"
import { dispatchInputOrSelect, getError } from "../Tools/formValidator"

export const SearchMail = ({ fields }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const [mail, setMail] = useState("")
    const [searchSuccess, setSS] = useState("")
    const [searchErr, setSE] = useState("")



    function handleSubmit(event) {
        event.preventDefault();
        axios.post(baseApi + '/user/password', { email: mail }, { headers: headerApi })
            .then(res => {
                setMail('')
                setSS(res.data.message)
            })
            .catch(err => setSE("Email non trouvé"))
    }


    return <section>
        <h2>Veuillez saisir le mail utilisé lors de votre inscription</h2>
        <b className="errorField"> {searchErr} </b>
        <b className="successField">{searchSuccess} </b>
        <form onSubmit={(event) => handleSubmit(event)}>
            <div className="formSegment">
                <label>Le mail</label>
                <section>
                    <input type="mail" placeholder="Mail de l'inscription"
                        onChange={(event) => setMail(event.target.value)} />
                </section>
            </div>
            <div className="formBtn">
                <button>Rechercher le mail</button>
            </div>
        </form>

    </section>
}


export const ChangePassword = ({ props }) => {
    const { apiInfos } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const fields = [allFields.password, allFields.password_confirmation]
    const { passwordId, passwordToken } = props
    const [formValues, setFormValues] = useState({ password: "", password_confirmation: "" })
    const [errors, setErrors] = useState([]);
    const [upError, setUE] = useState('');
    const [upSuccess, setUS] = useState('');
    const [btnLoad, setBtnLoad] = useState(false);




    const [viewUpdateForm, setVUF] = useState(false)


    function handleSubmit(event) {
        setBtnLoad(true)

        event.preventDefault()
        axios.post(baseApi + "/user/change-password",
            { id: passwordId, token: passwordToken, password: formValues.password },
            { headers: headerApi })
            .then(res => {
                if (res.data.message) {
                    setUS('Mot de passe modifié avec success')
                }
                setBtnLoad(false)
            })
            .catch(err => {
                // setBtnLoad(false)
                setUE('Une erreur s\'est produite')
                setTimeout(() => { setBtnLoad(false) }, 1000)
            })
    }

    useEffect(() => {
        // setVUF(true)
        axios.post(baseApi + "/user/password-reset",
            { id: passwordId, token: passwordToken },
            { headers: headerApi })
            .then(res => setVUF(true))
            .catch(err => setVUF(false))
    }, [props])

    return <section>
        {
            viewUpdateForm ?
                <>
                    <h2>Veuillez saisir le nouveau mot de passe</h2>
                    <b className="errorField"> {upError} </b>
                    <b className="successField">{upSuccess} </b>
                    <form onSubmit={event => handleSubmit(event)}>
                        {
                            fields.map((field, index) => <div className="formSegment"
                                key={'password field nb' + index}>
                                <label>{field.label} </label>
                                {
                                    dispatchInputOrSelect(formValues, setFormValues,
                                        field, handleUserFieldInfoChange, errors, setErrors)
                                }
                                {/* <span className=""></span> */}
                                {getError(field.name, errors)}
                            </div>)
                        }
                        <div className="formBtn">
                            {
                                errors.length > 0 || formValues.password === "" ||
                                    formValues.password != formValues.password_confirmation ?
                                    <button className="btnDisable" disabled>Enregistrer</button>
                                    : <FormSubBtn props={{ btnLoad, content: 'Enregistrer', className: " " }} />
                            }
                        </div>
                    </form>
                </>
                : <h2>Desolé,Vous ne pouvez pas modifié votre mot de passe</h2>
        }

    </section>
}