import moment from 'moment'

export const LastOperation = ({ props }) => {
    return <section className="dLastInfos">
        <h2>Dernières transactions</h2>
        <br />
        {
            props.lastOperation.map((item, index) => <div key={"last info nb" + index}>
                <p>
                    <b>Titre :</b>{item.titre.split('-')[1]}
                </p>
                <p>
                    <b>Fait le :</b> {moment(item.created_at).format('MMMM Do YYYY, h:mm:ss a')}
                </p>
                <p>
                    <b>Aide :</b>{item.aide}
                </p>
                <p>
                    <b>Statut :</b>{item.phase}
                </p>
            </div>)
        }
    </section>
}



export const DashHowItWork = ({ props }) => {
    //     userFullName
    // userMentor
    const { userFullName, userMentor } = props;
    return <section className="dGuide">
        <h2>Comment proceder ?</h2>
        <br />
        Mr/mdme <b>{userFullName}</b> Avant tout nous vous remercions d'avoir signé
        le contrat d'accompagnement France Assist.
        <br />
        Votre expert attitré est <b>{userMentor.nom + ' ' + userMentor.prenom}</b> .
        <p>
            Afin de pouvoir analyser votre dossier, nous avons besoin de <b>justificatifs</b>.
            <br />
            Plus <b>tôt</b> vous les enverrez, plus <b>tôt</b> nous pourrons avancer.
        </p>

        <h3>Documents à envoyer :</h3>

        <ul style={{ display: "flex", flexDirection: "column" }}>
            <li>* Carte d'identité ou passeport pour vous et votre éventuel conjoint et enfants.</li>
            <li>* Si vous êtes étranger : titre de séjour.</li>
            <li>
                * Justificatifs de revenus (fiches de paie, indemnités) des 3 derniers mois
                (pour vous et votre éventuel conjoint).
            </li>
            <li>
                * Contrat de location (4 premières pages) si vous êtes locataire.
            </li>
        </ul>

        <h3>Comment envoyer vos documents ?</h3>

        <p>
            En envoyant vos documents dans la rubrique Inserer un document sur votre espace france Assist
            N'oubliez pas de prendre RDV avec moi pour faire le point
            <a href={userMentor.cal} target="_blank"> {userMentor.cal} </a>
            Vous êtes perdu(e) ? Appelez-moi ou envoyez-moi un WhatsApp au <br />
            <a href=""> {userMentor.telephone} (gratuit)</a>
            .
            Ou par mail sur <a href=''>{userMentor.email}</a>
        </p>
    </section>
}