import { Route, Routes, useLocation } from "react-router-dom";
import './Assets/css/default.css';
import './Assets/css/card.css';
import './Assets/css/errorAndMaintenance.css';
import './Assets/css/app.css';
import './Assets/css/toast.css';

import './Assets/css/modal.css';
import "./Assets/css/slick/slick.css";
import "./Assets/css/slick/slick-theme.css";
import './Assets/css/materialdesignicons.min.css'
import './Assets/js/scroll.js';
import "moment/locale/fr";
import { Provider } from "react-redux";
import store from "./store";
import Maintenance from "./Routes/Maintenance";
import { GeneralNav } from "./GlobalComponents/navigation/navbar";
import Accueil from "./Routes/Accueil";
import { Footer } from "./GlobalComponents/footer/footer";
import Auth from "./Routes/Auth";
import Mot_de_passe from "./Routes/Mot_de_passe";
import { useEffect } from "react";
import Faq from "./Routes/Faq";
import { toggleMenuAndScrollBar } from "./Tools/animations";
import Contact from "./Routes/Contact";
import Politique_de_confidentialité from "./Routes/Pdc";
import Mention_légales from "./Routes/Ml";
import CGUV from "./Routes/Cguv";
import Simulations from "./Routes/Simulations";
import Guides from "./Routes/Guides";
import Dashboard from "./Routes/Dashboard";
import Documents from "./Routes/Documents";
import Souscription from "./Routes/Souscription";
import Aide_Offres from "./Routes/Aide-Offre";
import { AuthProvider } from "./Hooks/authHooks";
import { NotVisibleIfAuthed, RequireToBeAuthed } from "./middleware";
import Agenda from "./Routes/Agenda";
import Profil from "./Routes/Profil";
import SimulationGratuit from "./Routes/Simulation-gratuite";
import SimulationResultat from "./Routes/SimulationResultat";
import Ressources from "./Routes/Ressources";


function App() {
  const location = useLocation();
  const routesTables = [
    { link: '/', components: <Accueil />, requireAuth: 'both' },
    { link: '/Connexion', components: <Auth props={{ authType: 'connexion' }} />, requireAuth: 'notVisibleIfAuth' },
    { link: '/Inscription', components: <Auth props={{ authType: 'inscription' }} />, requireAuth: 'notVisibleIfAuth' },
    { link: '/password/mail', components: <Mot_de_passe />, requireAuth: false },
    { link: '/password/:passwordId/:passwordToken', components: <Mot_de_passe />, requireAuth: false },
    { link: '/Simulations', components: <Simulations />, requireAuth: 'both' },
    { link: '/Simulations/:typeSimulation', components: <Simulations />, requireAuth: 'both' },
    { link: '/Nos-Aides/:typeAide', components: <Aide_Offres />, requireAuth: 'both' },


    { link: '/Guides', components: <Guides />, requireAuth: 'both' },
    { link: '/Guides/:typeGuide', components: <Guides />, requireAuth: 'both' },

    { link: '/Souscription', components: <Souscription key={new Date()} />, requireAuth: false },
    { link: '/Souscription/:typeCommande', components: <Souscription key={new Date()} />, requireAuth: false },

    { link: '/Maintenance', components: <Maintenance />, requireAuth: false },
    {
      link: '/Simulation-gratuite', components: <SimulationGratuit />, requireAuth: false
    },
    { link: '/Simulation-resultat', components: <SimulationResultat />, requireAuth: false },


    { link: '/Contact', components: <Contact />, requireAuth: false },
    { link: '/Faq', components: <Faq />, requireAuth: false },
    { link: '/Faq/:qType/:qId', components: <Faq />, requireAuth: false },
    { link: '/Politique-de-confidentialite', components: <Politique_de_confidentialité />, requireAuth: false },
    { link: '/Mention-legales', components: <Mention_légales />, requireAuth: false },
    { link: '/CGV', components: <CGUV />, requireAuth: false },


    { link: '/Dashboard', components: <Dashboard />, requireAuth: true },
    { link: '/Documents/:documentAction', components: <Documents />, requireAuth: true },
    { link: '/Agenda', components: <Agenda />, requireAuth: true },
    { link: '/Ressources', components: <Ressources />, requireAuth: true },
    { link: '/Ressources/:typeRessources', components: <Ressources />, requireAuth: true },


    { link: '/Profil/:action', components: <Profil />, requireAuth: true },




  ]

  useEffect(() => {

    if (!location.pathname.includes('/Faq')) {
      window.scrollTo(0, 0);
    }
    if (document.querySelector('main').classList.contains('mainMoveRight')) {
      toggleMenuAndScrollBar()
    }

  }, [location]);


  return (
    <div className="App">
      <main>
        <AuthProvider>
          <header>
            <GeneralNav />
          </header>
          <Routes>

            {
              routesTables.map((route, index) => <Route key={'nim num route' + index}
                path={route.link}
                element={(() => {
                  if (route.requireAuth == true) {
                    return <RequireToBeAuthed>
                      {route.components}
                    </RequireToBeAuthed>
                  } else if (route.requireAuth == 'notVisibleIfAuth') {
                    return <NotVisibleIfAuthed>
                      {route.components}
                    </NotVisibleIfAuthed>
                  } else {
                    return ['/Simulation-gratuite', '/Simulation-resultat'].includes(route.link)
                      ? <Provider store={store}>
                        <div className="free-simulation">
                          {route.components}
                        </div>
                      </Provider>
                      : route.components
                  }
                })()} />)
            }

          </Routes>
        </AuthProvider>

      </main>
      <Footer />
    </div>
  );
}


