import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { FormOperationAlert } from "../GlobalComponents/Alert";
import { useAuth } from "../Hooks/authHooks";
import { ressourcesFields } from "../Raw/fields";
import { handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchInputOrSelect } from "../Tools/formValidator";

const Ressources = () => {
    const { typeRessources } = useParams();

    // const resourceView
    return <div className="ressources">
        <h1>Vos ressources</h1>
        {(() => {
            if (typeRessources) {
                return <ViewOrUpdateRessources props={{ typeRessources }} />
            } else {
                return <ListRessources />

            }
        })()}
    </div>
}



const ListRessources = () => {
    return <section>
        <h2>Actualisez vos ressources financières <br />(Net à payer avant impôt sur le revenu) </h2>
        <div>
            <p>Ressources trimestrielles</p>
            <Link to="/Ressources/trimestrielles">Actualiser</Link>
        </div>
        <div>
            <p>Ressources annuelles</p>
            <Link to="/Ressources/annuelles">Actualiser</Link>
        </div>
    </section>
}


const ViewOrUpdateRessources = ({ props }) => {

    const { apiInfos, notify } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const [currentRessources, setCR] = useState({});
    const [childRessources, setCHR] = useState([])
    useEffect(() => {
        axios.get(baseApi + "/user/ressources", { headers: headerApi })
            .then(res => {
                setCR(res.data)
                setFormValues({
                    salaire_annuelle: res.data.salaires_annuelles,
                    charge_deductible: res.data.charges_deductibles_annuelles,

                    trimestre_1: res.data.salaires_trimestrielles.trimestre_1,
                    trimestre_2: res.data.salaires_trimestrielles.trimestre_2,
                    trimestre_3: res.data.salaires_trimestrielles.trimestre_3,

                    salaires_enfants: res.data.salaires_enfants
                })
                setCHR(res.data.salaires_enfants)
            })
            .catch(err => console.log('d'))

    }, [])

    const { typeRessources } = props;
    const [errors, setErrors] = useState([])

    const formFields = {
        "annuelles": [
            ressourcesFields.salaire_annuelle, ressourcesFields.charge_deductible
        ],
        "trimestrielles": [
            ressourcesFields.il_ya_un_month,
            ressourcesFields.il_ya_deux_month,
            ressourcesFields.il_ya_trois_month,
        ]
    }

    const [formValues, setFormValues] = useState({
        salaire_annuelle: '',
        charge_deductible: '',

        trimestre_1: '',
        trimestre_2: '',
        trimestre_3: '',

        salaires_enfants: []

    })

    function handleSubmit(event) {
        event.preventDefault();
        let formData = null;
        let urlEnd = "";

        if (typeRessources == 'annuelles') {
            urlEnd = "/ressources-annuelles"
            formData = {
                salaires_annuelles: formValues.salaire_annuelle,
                charges_deductibles_annuelles: formValues.charge_deductible,
                salaires_annuelles_conjoint: 0,
                charges_deductibles_annuelles_conjoint: 0
            }
        } else {
            urlEnd = "/ressources-trimestrielles"
            formData = {
                salaires_trimestrielles: {
                    trimestre_1: formValues.trimestre_1,
                    trimestre_2: formValues.trimestre_2,
                    trimestre_3: formValues.trimestre_3,
                },
                salaires_trimestrielles_conjoint: {
                    trimestre_1: 0,
                    trimestre_2: 0,
                    trimestre_3: 0,
                },
                salaires_enfants: 0
            }
        }

        axios.put(baseApi + '/user' + urlEnd, formData, { headers: headerApi })
            .then(res => {
                console.log(res);
                if (res.data._id) {
                    notify('success', 'Ressources enregistré avec success')
                }
            })
            .catch(err => notify('error', 'Désolé ,une erreur est survenue'))

    }


    function handleChildSalaireChange(event) {
        let value = event.target.value;
        let name = event.target.name;

        let copy = formValues.salaires_enfants;
        if (formValues.salaires_enfants.length == 0) {
            copy = [{ id: name, salaires: value }]
        } else {
            copy.push({ id: name, salaires: value })
        }
        setFormValues({ ...formValues, salaires_enfants: copy })
    }


    return <section>
        <Link to="/Ressources"> <i className="mdi mdi-chevron-left"></i> Revenir a la liste des ressources</Link>
        <h2>Ressources {typeRessources} </h2>
        <form onSubmit={(event) => handleSubmit(event)}>

            {
                currentRessources._id ?
                    formFields[typeRessources].map((field, index) => <div key={"ressoiurce field nb" + index}
                        className="formSegment">
                        <label>{field.label}</label>
                        <section>
                            {
                                dispatchInputOrSelect(formValues, setFormValues,
                                    field, handleUserFieldInfoChange,
                                    errors, setErrors)
                            }
                        </section>
                    </div>) : ""
            }
            {
                // typeRessources == 'trimestrielles' && childRessources.length > 0 ?
                //     <>
                //         <h2>Notifier le salaire de vos enfants(si ils en recoivent) </h2>
                //         {
                //             currentRessources.salaires_enfants.map((cse, index) => <div key={"child salair nb" + index}
                //                 className="formSegment">
                //                 <label>Salaire enfant n* "1" </label>
                //                 <input type="number" value={cse.salaires}
                //                     name={'dk76s766'}
                //                     onChange={(event) => handleChildSalaireChange(event)} />
                //             </div>)
                //         }
                //     </>
                //     : ""
            }
            <div className="formBtn">
                <button>Enregistrer les ressources</button>
            </div>
        </form>
    </section>
}

export default Ressources;