import { useState } from 'react';
import { Link } from 'react-router-dom'
import { showMenuAndMoveMainAnimation } from '../../Tools/animations';
import { SimpleImage } from '../img/global'
import { GeneralSideBar } from './sidebar';
import { Modal } from '../message/modal';
import { authedNavLinks, generalNavLinks, guidesLinksList, offreTacheLinks, simulateurLinks } from '../../Raw/menuLinks';
import { useAuth } from '../../Hooks/authHooks';

export const GeneralNav = () => {
    const { authed } = useAuth()
    const [categoryIndex, setCategoryIndex] = useState(0)
    const [tgModal1, setTgModal1] = useState(false)
    const [tgModal2, setTgModal2] = useState(false)


    function dispatchNavLinks() {
        let linksTableCopy = generalNavLinks;
        if (authed) {
            const ltcFilter = linksTableCopy.filter(item => item.visibleIfAuthed == true)
            linksTableCopy = authedNavLinks.concat(ltcFilter);

        }
        return linksTableCopy;

    }

    const [menuLinks, setMenuLinks] = useState(dispatchNavLinks())

    function toggleDropDown(event, dropped = null) {
        if (dropped != null) {
            let droppedOnDom = document.querySelector(dropped);
            droppedOnDom.classList.toggle('isDropDownVisibleByHeightAnimation')
        } else {
            if (document.querySelector('.isDropDownVisibleByHeightAnimation') &&
                document.querySelector('.isDropDownVisibleByHeightAnimation')
                !== event.currentTarget.nextSibling) {
                document.querySelector('.isDropDownVisibleByHeightAnimation')
                    .classList.toggle('isDropDownVisibleByHeightAnimation')
            }
            event.currentTarget.nextSibling.classList.toggle('isDropDownVisibleByHeightAnimation')
        }

        if (event.currentTarget.children[0].classList.contains('mdi-rotate-90')) {
            event.currentTarget.children[0].classList.remove('mdi-rotate-90')
        } else {
            event.currentTarget.children[0].classList.add('mdi-rotate-90')
        }
    }




    return <>
        <nav className='generalNav'>

            <section className='gnAuthAndContact'>
                <div className=''>
                    <span className='notVisileOnPC' onClick={(event) => toggleDropDown(event)}>
                        Contact <i className='mdi mdi-chevron-right'></i>
                    </span>
                    <section>
                        <span><i className='mdi mdi-phone'></i> 04 65 84 08 48</span>
                        <span><i className='mdi mdi-google-maps'></i>
                            36 Avenue General de Gaulle, 69110 Sainte‑Foy‑lès‑Lyon</span>
                    </section>
                </div>
                <div>
                    <span className='notVisileOnPC' onClick={(event) => toggleDropDown(event)}>
                        Mon compte <i className='mdi mdi-chevron-right'></i>
                    </span>
                    <section><AuthLinks props={{ authed }} /></section>
                </div>
            </section>

            <section className='gnLogo'>
                <SimpleImage props={{ src: 'logo.png', alt: 'Logo non Img', classname: '' }} />
            </section>

            <section className='gnNavLinks'>
                {dispatchNavLinks().filter((gnl) => gnl.subLinks.length === 0).map((link, index) => <div
                    key={'nav link' + index}>
                    {
                        <Link to={link.link} >{link.label}</Link>
                    }
                </div>)}
            </section>


            <section className='gnHambuger' onClick={(event) => showMenuAndMoveMainAnimation()}>
                <span>Menu</span>
                <i className='mdi mdi-menu'></i>
            </section>

            <section className='gnSearchBar'>
                <b onClick={(event) => {
                    setTgModal2(true)
                }}><i className='mdi mdi-magnify'></i></b>
            </section>


            <section className='gnCategorys'>
                {
                    menuLinks.map((item, i1) => {
                        return item.subLinks.length > 0 ? <div key={'categorie item' + i1}>
                            <b onClick={(event) => {
                                setCategoryIndex(i1)
                                setTgModal1(true)
                            }}
                            >{item.label}<i className='mdi mdi-chevron-right'></i> </b>
                        </div> : ''
                    })
                }
                <Modal props={{
                    toggleModal: tgModal1,
                    content: <CategoryModal props={{
                        menuObject: menuLinks,
                        menuIndex: categoryIndex,
                        nextMenuKey: 'subLinks',
                        setToggleModal: setTgModal1
                    }} />,
                    setToggleModal: setTgModal1,
                    className: 'gnModalSubCategorys'
                }} />

                <Modal props={{
                    toggleModal: tgModal2,
                    content: <SearchBar {...setTgModal2} />,
                    setToggleModal: setTgModal2,
                    className: 'searchModal'
                }} />

            </section>
        </nav >
        <GeneralSideBar props={{ menuObject: dispatchNavLinks(), subMenuKey: 'subLinks', authed }} />
    </>
}


const CategoryModal = ({ props }) => {
    const { menuObject, nextMenuKey, menuIndex } = props;
    const [categoryIndex2, setCategoryIndex2] = useState(0)
    return <div className="gnMSC">
        <section className="horizontalScrollTabs">
            {
                menuObject[menuIndex]
                [nextMenuKey][categoryIndex2]
                [nextMenuKey] ?
                    menuObject[menuIndex][nextMenuKey].map((sc1, i2) => <label
                        key={'sub categorie 1 label' + i2} htmlFor={'sc1-label' + i2}>
                        <b className={i2 === categoryIndex2 ? 'tabHasSelected' : ''}>{sc1.label} </b>
                        <input type='radio'
                            id={'sc1-label' + i2}
                            name={'sc1-label'}
                            value={i2}
                            onChange={() => { setCategoryIndex2(i2) }}
                        />
                    </label>) : ''
            }
        </section>
        <ul >
            {
                menuObject[menuIndex]
                [nextMenuKey][categoryIndex2]
                [nextMenuKey] ?
                    menuObject[menuIndex]
                    [nextMenuKey][categoryIndex2]
                    [nextMenuKey].map((sc2, i3) => <li
                        key={'subsub Link nb' + i3}>
                        <span>{sc2.label}</span>
                        <Link to={sc2.link}>Aller a</Link>
                    </li>)
                    : menuObject[menuIndex]
                    [nextMenuKey].map((sc2, i3) => <li
                        key={'subsub Link nb' + i3}>
                        <span>{sc2.label}</span>
                        <Link
                            onClick={() => props.setToggleModal(false)}
                            to={sc2.link}>Aller a</Link>
                    </li>)
            }
        </ul>
    </div>
}

export const AuthLinks = ({ props }) => {
    const { logout } = useAuth()
    return <>
        {
            !props.authed ? <>
                <Link to='/Connexion'>Connexion</Link>
                <Link to='/Inscription'>Inscription</Link>
            </>
                : <>
                    <Link to='/Connexion'>Mon Profil</Link>
                    <Link to='' onClick={(event) => {
                        event.preventDefault();
                        logout()
                    }}>
                        <i className='mdi mdi-logout'></i> Deconnexion</Link>
                </>
        }
    </>
}

const SearchBar = ({ setTgModal2 }) => {
    // generalNavLinks
    const [searchRes, setSR] = useState(null)

    function handleSearch(e) {
        let value = e.target.value;

        const filter1 = generalNavLinks.filter((el) => el.label.toLocaleLowerCase().includes(value)
            && el.subLinks.length == 0);
        const filter2 = guidesLinksList.filter((el) => el.label.toLocaleLowerCase().includes(value));
        const filter3 = simulateurLinks.filter((el) => el.label.toLocaleLowerCase().includes(value));
        const filter4 = offreTacheLinks.filter((el) => el.label.toLocaleLowerCase().includes(value));

        setSR(filter1.concat(filter2).concat(filter3).concat(filter4))
    }
    // [0, 0, 0, 0, 0]
    return <div className='searchBar'>
        <section className='sb-input'>
            <h2>Rechercher</h2>
            <input type="search" placeholder='Recherchez ici....'
                onChange={(event) => handleSearch(event)} />
        </section>

        <section className='sb-results'>
            {
                (() => {
                    if (searchRes == null) {
                        return <p>Aucune recherche effectué</p>
                    } else if (searchRes.length == 0) {
                        return <p>Aucun resultat</p>
                    } else {
                        return <>
                            {
                                searchRes.map((searchResult, index) => <div key={"search re nb" + index}>
                                    {/* <span>{searchResult.label}</span>
                                    <Link to={searchResult.link}
                                        onClick={() => setTgModal2(false)}>Consulter </Link> */}
                                    <Link to={searchResult.link}
                                        onClick={() => setTgModal2(false)}>{searchResult.label} </Link>
                                </div>)
                            }
                        </>
                    }
                })()
            }
        </section>
    </div>
}