import { motion } from "framer-motion";
import React, { useEffect, useLayoutEffect, useState } from "react";
import "./foyer.css";

function Foyer({
  setSection,
  setProgress,
  setData,
  setModal,
  modalData,
  setModalData,
}) {
  var today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1; //January is 0!
  var yyyy = today.getFullYear();
  if (dd < 10) {
    dd = "0" + dd;
  }
  if (mm < 10) {
    mm = "0" + mm;
  }

  today = yyyy + "-" + mm + "-" + dd;
  const [page, setPage] = useState(1);
  const [foyer, setFoyer] = useState({
    enfantsACharge: "",
    enfants: "",
    etatLegal: "",
    visSeul: "",
    nbrFrere: "",
    nbrFrereEnseignementSup: "",
    conjointAge: "",
    conjointStatutPersonnel: "",
    conjointStatutPersonnel2: "",
    conjointFormation: "",
    conjointPartirEtranger: "",
    conjointBourse: "",
    conjointBourseEchelon: "",
    conjointAideFinanciere: "",
    conjointTravailleFranceDemandeurEmploi: "",
    conjointDernierContratTravail: "",
    conjointTauxHandicap: "",
    conjointPensionInvalidite:""
  });
  useEffect(() => {
    setFoyer((foyer) => {
      return { ...foyer, enfants: modalData };
    });
  }, [modalData]);
  function singleSelect(e, cl) {
    let doc = document.querySelectorAll(cl);
    if (!foyer[e.target.id] || foyer[e.target.id] !== e.target.innerText) {
      setFoyer({ ...foyer, [e.target.id]: e.target.innerText });
      for (let i = 0; i < doc.length; i++) {
        doc[i].classList.remove("selected");
      }
      e.target.classList.add("selected");
    } else {
      setFoyer({ ...foyer, [e.target.id]: "" });
      e.target.classList.remove("selected");
    }
  }
  function multiSelect(e, cl) {
    if (!foyer[e.target.id].includes(e.target.innerText)) {
      setFoyer({
        ...foyer,
        [e.target.id]: [...foyer[e.target.id], e.target.innerText],
      });

      e.target.classList.add("selected");
    } else {
      setFoyer({
        ...foyer,
        [e.target.id]: foyer[e.target.id].filter(
          (el) => el !== e.target.innerText
        ),
      });
      // profil[e.target.id].filter(el=>el!==e.target.id)
      e.target.classList.remove("selected");
    }
  }

  useLayoutEffect(() => {
    let enfantsACharge = document.querySelectorAll(".enfantsACharge");
    let etatLegal = document.querySelectorAll(".etatLegal");
    let visSeul = document.querySelectorAll(".visSeul");
    let conjointStatutPersonnel = document.querySelectorAll(
      ".conjointStatutPersonnel"
    );
    let conjointStatutPersonnel2 = document.querySelectorAll(
      ".conjointStatutPersonnel2"
    );
    let conjointFormation = document.querySelectorAll(".conjointFormation");
    let conjointPartirEtranger = document.querySelectorAll(
      ".conjointPartirEtranger"
    );
    let conjointBourse = document.querySelectorAll(".conjointBourse");
    let conjointBourseEchelon = document.querySelectorAll(
      ".conjointBourseEchelon"
    );
    let conjointAideFinanciere = document.querySelectorAll(
      ".conjointAideFinanciere"
    );
    let conjointTravailleFranceDemandeurEmploi = document.querySelectorAll(
      ".conjointTravailleFranceDemandeurEmploi"
    );
    let conjointTauxHandicap = document.querySelectorAll(
      ".conjointTauxHandicap"
    );
    let conjointPensionInvalidite = document.querySelectorAll(
      ".conjointPensionInvalidite"
    );

    if (foyer.enfantsACharge && enfantsACharge) {
      for (let i = 0; i < enfantsACharge.length; i++) {
        if (enfantsACharge[i].innerText === foyer.enfantsACharge)
          enfantsACharge[i].classList.add("selected");
      }
    }
    if (foyer.etatLegal && etatLegal) {
      for (let i = 0; i < etatLegal.length; i++) {
        if (etatLegal[i].innerText === foyer.etatLegal)
          etatLegal[i].classList.add("selected");
      }
    }
    if (foyer.visSeul && visSeul) {
      for (let i = 0; i < visSeul.length; i++) {
        if (visSeul[i].innerText === foyer.visSeul)
          visSeul[i].classList.add("selected");
      }
    }
    if (foyer.conjointStatutPersonnel && conjointStatutPersonnel) {
      for (let i = 0; i < conjointStatutPersonnel.length; i++) {
        if (
          conjointStatutPersonnel[i].innerText === foyer.conjointStatutPersonnel
        )
          conjointStatutPersonnel[i].classList.add("selected");
      }
    }
    if (foyer.conjointStatutPersonnel2.length && conjointStatutPersonnel2) {
      for (let i = 0; i < conjointStatutPersonnel2.length; i++) {
        if (
          foyer.conjointStatutPersonnel2.includes(
            conjointStatutPersonnel2[i].innerText
          )
        )
          conjointStatutPersonnel2[i].classList.add("selected");
      }
    }
    if (foyer.conjointFormation && conjointFormation) {
      for (let i = 0; i < conjointFormation.length; i++) {
        if (conjointFormation[i].innerText === foyer.conjointFormation)
          conjointFormation[i].classList.add("selected");
      }
    }
    if (foyer.conjointPartirEtranger && conjointPartirEtranger) {
      for (let i = 0; i < conjointPartirEtranger.length; i++) {
        if (
          conjointPartirEtranger[i].innerText === foyer.conjointPartirEtranger
        )
          conjointPartirEtranger[i].classList.add("selected");
      }
    }
    if (foyer.conjointBourse && conjointBourse) {
      for (let i = 0; i < conjointBourse.length; i++) {
        if (conjointBourse[i].innerText === foyer.conjointBourse)
          conjointBourse[i].classList.add("selected");
      }
    }
    if (foyer.conjointBourseEchelon && conjointBourseEchelon) {
      for (let i = 0; i < conjointBourseEchelon.length; i++) {
        if (conjointBourseEchelon[i].innerText === foyer.conjointBourseEchelon)
          conjointBourseEchelon[i].classList.add("selected");
      }
    }
    if (foyer.conjointAideFinanciere && conjointAideFinanciere) {
      for (let i = 0; i < conjointAideFinanciere.length; i++) {
        if (
          conjointAideFinanciere[i].innerText === foyer.conjointAideFinanciere
        )
          conjointAideFinanciere[i].classList.add("selected");
      }
    }
    if (
      foyer.conjointTravailleFranceDemandeurEmploi &&
      conjointTravailleFranceDemandeurEmploi
    ) {
      for (let i = 0; i < conjointTravailleFranceDemandeurEmploi.length; i++) {
        if (
          conjointTravailleFranceDemandeurEmploi[i].innerText ===
          foyer.conjointTravailleFranceDemandeurEmploi
        )
          conjointTravailleFranceDemandeurEmploi[i].classList.add("selected");
      }
    }
    if (foyer.conjointTauxHandicap && conjointTauxHandicap) {
      for (let i = 0; i < conjointTauxHandicap.length; i++) {
        if (conjointTauxHandicap[i].innerText === foyer.conjointTauxHandicap)
          conjointTauxHandicap[i].classList.add("selected");
      }
    }
    if (foyer.conjointPensionInvalidite && conjointPensionInvalidite) {
      for (let i = 0; i < conjointPensionInvalidite.length; i++) {
        if (conjointPensionInvalidite[i].innerText === foyer.conjointPensionInvalidite)
        conjointPensionInvalidite[i].classList.add("selected");
      }
    }
  }, [foyer, page]);

  useEffect(() => {
    window.scrollTo(0, 160);
  }, [page]);

  const normalizeAgeNumber = (value) => {
    return value.replace(/\D/gi, "") || "";
  };

  const normalizeDateNumber = (value) => {
    if (new Date(value) > new Date(today)) return "";
    else return value;
  };

  return (
    <>
      {page === 1 && (
        <div className="simulateur-nationalite">
          <span>
            J’ai des enfants à charge
            <span style={{ color: "#3c3c3c", fontSize: 13, display: "block" }}>
              Les personnes de moins de 25 ans ou handicapées dont vous assumez
              la responsabilité, même sans lien de parenté
            </span>
          </span>
          <div>
            <div
              className="enfantsACharge"
              onClick={(e) => singleSelect(e, ".enfantsACharge")}
              id="enfantsACharge"
            >
              Oui
            </div>
            <div
              className="enfantsACharge"
              onClick={(e) => singleSelect(e, ".enfantsACharge")}
              id="enfantsACharge"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
                setSection("profil");
                setProgress(20);
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.enfantsACharge}
              style={{ backgroundColor: foyer.enfantsACharge && "#2e74b9" }}
              onClick={() => {
                switch (foyer.enfantsACharge) {
                  case "Oui":
                    setPage(1.1);
                    break;
                  default:
                    setModalData([]);
                    setPage(2);
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 1.1 && (
        <div className="simulateur-enfants">
          <span>
            Mes enfants à charge
            <span
              style={{
                color: "#3c3c3c",
                fontSize: 13,
                display: "block",
              }}
            >
              Ou personnes handicapées
            </span>
          </span>
          <div className="enfants-map">
            {modalData.length ? (
              <p>
                {modalData.length} enfant{modalData.length === 1 ? "" : "s"}
              </p>
            ) : null}
            {modalData.map((el) => (
              <motion.div layout key={el.id}>
                <div>
                  <div>
                    <div>{el.age} ans</div>
                    {el.statut.map((el, id) => (
                      <div key={id}>{el}</div>
                    ))}
                  </div>
                  <div>
                    <i
                      className="far fa-trash-alt"
                      onClick={() =>
                        setModalData((data) =>
                          data.filter((elm) => elm.id !== el.id)
                        )
                      }
                    ></i>
                  </div>
                </div>
                <div
                  style={{
                    marginTop:
                      !el.echelonDeBourse &&
                      !el.tauxHandicap &&
                      !el.revenu &&
                      0,
                    paddingTop:
                      !el.echelonDeBourse &&
                      !el.tauxHandicap &&
                      !el.revenu &&
                      0,
                    borderTop:
                      !el.echelonDeBourse &&
                      !el.tauxHandicap &&
                      !el.revenu &&
                      "none",
                  }}
                >
                  {el.echelonDeBourse ? (
                    <div>
                      <div>Echelon de bourse</div>
                      <div>{el.echelonDeBourse}</div>
                    </div>
                  ) : null}
                  {el.tauxHandicap ? (
                    <div>
                      <div>Taux de handicap</div>
                      <div>{el.tauxHandicap}</div>
                    </div>
                  ) : null}
                  {el.revenu ? (
                    <div>
                      <div>Revenus</div>
                      <div>{el.revenu}€</div>
                    </div>
                  ) : null}
                </div>
              </motion.div>
            ))}
          </div>
          <div
            className="enfants-ajout"
            onClick={() => {
              setModal((modal) => !modal);
              document.querySelector(".App").style.overflowY = "hidden";
              document.querySelector(".App").style.height = "100vh";
              document.body.style.overflowY = "hidden";
              document.body.style.height = "100vh";
            }}
          >
            <div>Ajouter un enfant</div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
                setPage(1);
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!modalData.length}
              style={{ backgroundColor: !!modalData.length && "#2e74b9" }}
              onClick={() => setPage(2)}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 2 && (
        <div className="simulateur-nationalite">
          <span>Je suis</span>
          <div>
            <div
              className="etatLegal"
              onClick={(e) => singleSelect(e, ".etatLegal")}
              id="etatLegal"
            >
              Célibataire
            </div>
            <div
              className="etatLegal"
              onClick={(e) => singleSelect(e, ".etatLegal")}
              id="etatLegal"
            >
              Marié(e)
            </div>
            <div
              className="etatLegal"
              onClick={(e) => singleSelect(e, ".etatLegal")}
              id="etatLegal"
            >
              Pacsé(e)
            </div>
            <div
              className="etatLegal"
              onClick={(e) => singleSelect(e, ".etatLegal")}
              id="etatLegal"
            >
              En concubinage
            </div>
            <div
              className="etatLegal"
              onClick={(e) => singleSelect(e, ".etatLegal")}
              id="etatLegal"
            >
              Veuf(ve)
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
                setPage(1);
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.etatLegal}
              style={{ backgroundColor: foyer.etatLegal && "#2e74b9" }}
              onClick={() => {
                switch (foyer.etatLegal) {
                  case "Célibataire":
                    return setPage(2.1);

                  default:
                    setFoyer({ ...foyer, visSeul: "" });
                    // return setPage(3);
                    switch (foyer.etatLegal) {
                      case "Marié(e)":
                      case "Pacsé(e)":
                        case "En concubinage":
                        setPage(5);
                        break;
    
                      default:
                        setSection("mon logement");
                        setProgress(60);
                        setData((data) => {
                          return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                        });
                        break;
                    }
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
      {page === 2.1 && (
        <div className="simulateur-nationalite">
          <span>Je vis seul(e) depuis</span>
          <div>
            <div
              className="visSeul"
              onClick={(e) => singleSelect(e, ".visSeul")}
              id="visSeul"
            >
              Moins de 18 mois
            </div>
            <div
              className="visSeul"
              onClick={(e) => singleSelect(e, ".visSeul")}
              id="visSeul"
            >
              Plus de 18 mois
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => {
                setPage(2);
              }}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.visSeul}
              style={{ backgroundColor: foyer.visSeul && "#2e74b9" }}
              onClick={() => {
                // setPage(3);
                switch (foyer.etatLegal) {
                  case "Marié(e)":
                  case "Pacsé(e)":
                    case "En concubinage":
                    setPage(5);
                    break;

                  default:
                    setSection("mon logement");
                    setProgress(60);
                    setData((data) => {
                      return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                    });
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
      {page === 3 && (
        <>
          <div className="simulateur-date">
            <span>
              Nombre de frères et soeurs à la charge de mes parents
              <span
                style={{ color: "#3c3c3c", fontSize: 13, display: "block" }}
              >
                Sans me compter
              </span>
            </span>
            <div>
              <input
                style={{ width: "7ch" }}
                // ref={ageRef}
                value={foyer.nbrFrere}
                type="tel"
                autoFocus
                maxLength="2"
                placeholder="2"
                onChange={(e) => {
                  e.target.value = normalizeAgeNumber(e.target.value);
                  setFoyer({
                    ...foyer,
                    nbrFrere: e.target.value,
                  });
                }}
              />
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(2)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.nbrFrere}
              style={{
                backgroundColor: foyer.nbrFrere && "#2e74b9",
              }}
              onClick={() => {
                setPage(4);
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </>
      )}
      {page === 4 && (
        <>
          <div className="simulateur-date">
            <span>
              Nombre de frères et soeurs étudiant dans l’enseignement supérieur
            </span>
            <div>
              <input
                style={{ width: "7ch" }}
                // ref={ageRef}
                value={foyer.nbrFrereEnseignementSup}
                type="tel"
                autoFocus
                maxLength="2"
                placeholder="2"
                onChange={(e) => {
                  e.target.value = normalizeAgeNumber(e.target.value);
                  setFoyer({
                    ...foyer,
                    nbrFrereEnseignementSup: e.target.value,
                  });
                }}
              />
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(3)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.nbrFrereEnseignementSup}
              style={{
                backgroundColor: foyer.nbrFrereEnseignementSup && "#2e74b9",
              }}
              onClick={() => {
                switch (foyer.etatLegal) {
                  case "Marié(e)":
                  case "Pacsé(e)":
                    case "En concubinage":
                    setPage(5);
                    break;

                  default:
                    setSection("mon logement");
                    setProgress(60);
                    setData((data) => {
                      return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                    });
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </>
      )}

      {page === 5 && (
        <>
          <div className="simulateur-age">
            <span>Mon conjoint a</span>
            <div>
              <input
                // ref={ageRef}
                value={foyer.conjointAge}
                type="tel"
                placeholder="24"
                autoFocus
                maxLength="2"
                onChange={(e) => {
                  e.target.value = normalizeAgeNumber(e.target.value);
                  setFoyer({ ...foyer, conjointAge: e.target.value });
                }}
              />
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
            <span>ans</span>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(4)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={foyer.conjointAge <= 14}
              style={{ backgroundColor: foyer.conjointAge > 14 && "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </>
      )}

      {page === 6 && (
        <div className="simulateur-nationalite">
          <span>
            Mon conjoint est
            <span style={{ color: "	#3c3c3c", fontSize: 13, display: "block" }}>
              Plusieurs réponses possibles
            </span>
          </span>
          <div>
            <div
              className="conjointStatutPersonnel"
              onClick={(e) => singleSelect(e, ".conjointStatutPersonnel")}
              id="conjointStatutPersonnel"
            >
              Étudiant(e)
            </div>
            <div
              className="conjointStatutPersonnel"
              onClick={(e) => singleSelect(e, ".conjointStatutPersonnel")}
              id="conjointStatutPersonnel"
            >
              Apprenti(e)
            </div>
            <div
              className="conjointStatutPersonnel"
              onClick={(e) => singleSelect(e, ".conjointStatutPersonnel")}
              id="conjointStatutPersonnel"
            >
              Retraité(e)
            </div>
            <div
              className="conjointStatutPersonnel"
              onClick={(e) => singleSelect(e, ".conjointStatutPersonnel")}
              id="conjointStatutPersonnel"
            >
              Demandeur d’emploi
            </div>
            <div
              className="conjointStatutPersonnel2"
              onClick={(e) => multiSelect(e, ".conjointStatutPersonnel2")}
              id="conjointStatutPersonnel2"
            >
              Enceinte
            </div>
            <div
              className="conjointStatutPersonnel2"
              onClick={(e) => multiSelect(e, ".conjointStatutPersonnel2")}
              id="conjointStatutPersonnel2"
            >
              En situation de handicap
            </div>
            <div
              className="conjointStatutPersonnel2"
              onClick={(e) => multiSelect(e, ".conjointStatutPersonnel2")}
              id="conjointStatutPersonnel2"
            >
              Inapte au travail
            </div>
            {/* <div className="statut-personnel-multi" onClick={(e)=>multiSelect(e,".statut-personnel-multi")} id="statutPersonnel2">
            Intermittent du spectacle
            </div> */}
            <div
              className="conjointStatutPersonnel2"
              onClick={(e) => multiSelect(e, ".conjointStatutPersonnel2")}
              id="conjointStatutPersonnel2"
            >
              Actif (salarié / indépendant)
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(5)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={
                !foyer.conjointStatutPersonnel &&
                !foyer.conjointStatutPersonnel2.length
              }
              style={{
                backgroundColor:
                  !!(
                    foyer.conjointStatutPersonnel ||
                    foyer.conjointStatutPersonnel2.length
                  ) && "#2e74b9",
              }}
              onClick={() => {
                switch (foyer.conjointStatutPersonnel) {
                  case "Étudiant(e)":
                  case "Apprenti(e)":
                    setFoyer({
                      ...foyer,
                      conjointFormation: "",
                      conjointPartirEtranger: "",
                      conjointBourse: "",
                      conjointBourseEchelon: "",
                      conjointAideFinanciere: "",
                      conjointTravailleFranceDemandeurEmploi: "",
                      conjointDernierContratTravail: "",
                      conjointTauxHandicap: "",
                      conjointPensionInvalidite:""
                    });
                    return setPage(6.1);
                  // break
                  case "Retraité(e)":
                    setFoyer({
                      ...foyer,
                      conjointFormation: "",
                      conjointPartirEtranger: "",
                      conjointBourse: "",
                      conjointBourseEchelon: "",
                      conjointAideFinanciere: "",
                      conjointTravailleFranceDemandeurEmploi: "",
                      conjointDernierContratTravail: "",
                      conjointTauxHandicap: "",
                      conjointPensionInvalidite:""
                    });
                    if (
                      foyer.conjointStatutPersonnel2.includes(
                        "En situation de handicap"
                      )
                    )
                      return setPage(6.8);
                    if (
                      foyer.conjointStatutPersonnel2.includes(
                        "Inapte au travail"
                      )
                    )
                      return setPage(6.9);

                    setSection("mon logement");
                    setProgress(60);
                    setData((data) => {
                      return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                    });
                    break;
                  case "Demandeur d’emploi":
                    setFoyer({
                      ...foyer,
                      conjointFormation: "",
                      conjointPartirEtranger: "",
                      conjointBourse: "",
                      conjointBourseEchelon: "",
                      conjointAideFinanciere: "",
                      conjointTravailleFranceDemandeurEmploi: "",
                      conjointDernierContratTravail: "",
                      conjointTauxHandicap: "",
                      conjointPensionInvalidite:""
                    });
                    return setPage(6.6);
                  // break
                  default:
                    break;
                }
                if (foyer.conjointStatutPersonnel2.includes("Enceinte")) {
                  setFoyer({
                    ...foyer,
                    conjointFormation: "",
                    conjointPartirEtranger: "",
                    conjointBourse: "",
                    conjointBourseEchelon: "",
                    conjointAideFinanciere: "",
                    conjointTravailleFranceDemandeurEmploi: "",
                    conjointDernierContratTravail: "",
                    conjointTauxHandicap: "",
                    conjointPensionInvalidite:""
                  });
                  if (
                    foyer.conjointStatutPersonnel2.includes(
                      "En situation de handicap"
                    )
                  )
                    return setPage(6.8);
                  if (
                    foyer.conjointStatutPersonnel2.includes("Inapte au travail")
                  )
                    return setPage(6.9);

                  setSection("mon logement");
                  setProgress(60);
                  setData((data) => {
                    return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                  });
                }
                if (
                  foyer.conjointStatutPersonnel2.includes(
                    "En situation de handicap"
                  )
                ) {
                  setFoyer({
                    ...foyer,
                    conjointFormation: "",
                    conjointPartirEtranger: "",
                    conjointBourse: "",
                    conjointBourseEchelon: "",
                    conjointAideFinanciere: "",
                    conjointTravailleFranceDemandeurEmploi: "",
                    conjointDernierContratTravail: "",
                    conjointTauxHandicap: "",
                    conjointPensionInvalidite:""
                  });
                  return setPage(6.8);
                }
                if (
                  foyer.conjointStatutPersonnel2.includes("Inapte au travail")
                ) {
                  setFoyer({
                    ...foyer,
                    conjointFormation: "",
                    conjointPartirEtranger: "",
                    conjointBourse: "",
                    conjointBourseEchelon: "",
                    conjointAideFinanciere: "",
                    conjointTravailleFranceDemandeurEmploi: "",
                    conjointDernierContratTravail: "",
                    conjointTauxHandicap: "",
                    conjointPensionInvalidite:""
                  });
                  return setPage(6.9);
                }
                if (
                  foyer.conjointStatutPersonnel2.includes(
                    "Actif (salarié / indépendant)"
                  )
                ) {
                  setFoyer({
                    ...foyer,
                    conjointFormation: "",
                    conjointPartirEtranger: "",
                    conjointBourse: "",
                    conjointBourseEchelon: "",
                    conjointAideFinanciere: "",
                    conjointTravailleFranceDemandeurEmploi: "",
                    conjointDernierContratTravail: "",
                    conjointTauxHandicap: "",
                    conjointPensionInvalidite:""
                  });
                  setSection("mon logement");
                  setProgress(60);
                  setData((data) => {
                    return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                  });
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
      {page === 6.1 && (
        <div className="simulateur-nationalite">
          <span>Il/elle suit une formation en</span>
          <div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Apprentissage
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Lycée
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Lycée professionnel
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Licence
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Master
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Doctorat
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              BTS ou DUT
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Sanitaire et sociale
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Stagiaire
            </div>
            <div
              className="conjointFormation"
              onClick={(e) => singleSelect(e, ".conjointFormation")}
              id="conjointFormation"
            >
              Autre
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointFormation}
              style={{ backgroundColor: foyer.conjointFormation && "#2e74b9" }}
              onClick={() => {
                switch (foyer.conjointFormation) {
                  case "Apprentissage":
                    setFoyer({
                      ...foyer,
                      conjointBourse: "",
                      conjointBourseEchelon: "",
                      conjointAideFinanciere: "",
                    });
                    setPage(6.2);
                    break;
                  default:
                    setFoyer({
                      ...foyer,
                      conjointPartirEtranger: "",
                    });
                    setPage(6.3);
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.2 && (
        <div className="simulateur-nationalite">
          <span>
            Il/elle compte partir à l’étranger dans le cadre de ses études
          </span>
          <div>
            <div
              className="conjointPartirEtranger"
              onClick={(e) => singleSelect(e, ".conjointPartirEtranger")}
              id="conjointPartirEtranger"
            >
              Oui
            </div>
            <div
              className="conjointPartirEtranger"
              onClick={(e) => singleSelect(e, ".conjointPartirEtranger")}
              id="conjointPartirEtranger"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointPartirEtranger}
              style={{
                backgroundColor: foyer.conjointPartirEtranger && "#2e74b9",
              }}
              onClick={() => {
                if (
                  foyer.conjointStatutPersonnel2.includes(
                    "En situation de handicap"
                  )
                )
                  return setPage(6.8);
                if (
                  foyer.conjointStatutPersonnel2.includes("Inapte au travail")
                )
                  return setPage(6.9);

                setSection("mon logement");
                setProgress(60);
                setData((data) => {
                  return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                });
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.3 && (
        <div className="simulateur-nationalite">
          <span>Mon conjoint est boursier :</span>
          <div>
            <div
              className="conjointBourse"
              onClick={(e) => singleSelect(e, ".conjointBourse")}
              id="conjointBourse"
            >
              Oui
            </div>
            <div
              className="conjointBourse"
              onClick={(e) => singleSelect(e, ".conjointBourse")}
              id="conjointBourse"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(2)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointBourse}
              style={{ backgroundColor: foyer.conjointBourse && "#2e74b9" }}
              onClick={() => {
                switch (foyer.conjointBourse) {
                  case "Oui":
                    setPage(6.4);
                    break;
                  default:
                    setPage(6.5);
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.4 && (
        <div className="simulateur-nationalite">
          <span>Quel est son échelon de bourse ?</span>
          <div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              0
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              1
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              2
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              3
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              4
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              5
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              6
            </div>
            <div
              className="conjointBourseEchelon"
              onClick={(e) => singleSelect(e, ".conjointBourseEchelon")}
              id="conjointBourseEchelon"
            >
              7
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointBourseEchelon}
              style={{
                backgroundColor: foyer.conjointBourseEchelon && "#2e74b9",
              }}
              onClick={() => {
                setPage(6.2);
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.5 && (
        <div className="simulateur-nationalite">
          <span>Ses parents l’aident financièrement :</span>
          <div>
            <div
              className="conjointAideFinanciere"
              onClick={(e) => singleSelect(e, ".conjointAideFinanciere")}
              id="conjointAideFinanciere"
            >
              Oui
            </div>
            <div
              className="conjointAideFinanciere"
              onClick={(e) => singleSelect(e, ".conjointAideFinanciere")}
              id="conjointAideFinanciere"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointAideFinanciere}
              style={{
                backgroundColor: foyer.conjointAideFinanciere && "#2e74b9",
              }}
              onClick={() => {
                setPage(6.2);
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.6 && (
        <div className="simulateur-nationalite">
          <span>Il/elle a déjà travaillé en France</span>
          <div>
            <div
              className="conjointTravailleFranceDemandeurEmploi"
              onClick={(e) =>
                singleSelect(e, ".conjointTravailleFranceDemandeurEmploi")
              }
              id="conjointTravailleFranceDemandeurEmploi"
            >
              Oui
            </div>
            <div
              className="conjointTravailleFranceDemandeurEmploi"
              onClick={(e) =>
                singleSelect(e, ".conjointTravailleFranceDemandeurEmploi")
              }
              id="conjointTravailleFranceDemandeurEmploi"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(2)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointTravailleFranceDemandeurEmploi}
              style={{
                backgroundColor:
                  foyer.conjointTravailleFranceDemandeurEmploi && "#2e74b9",
              }}
              onClick={() => {
                switch (foyer.conjointTravailleFranceDemandeurEmploi) {
                  case "Oui":
                    setPage(6.7);
                    break;
                  default:
                    if (
                      foyer.conjointStatutPersonnel2.includes(
                        "En situation de handicap"
                      )
                    )
                      return setPage(6.8);
                    if (
                      foyer.conjointStatutPersonnel2.includes(
                        "Inapte au travail"
                      )
                    )
                      return setPage(6.9);

                    setSection("mon logement");
                    setProgress(60);
                    setData((data) => {
                      return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                    });
                    break;
                }
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.7 && (
        <>
          <div className="simulateur-date">
            <span>Son dernier contrat de travail s'est terminé en</span>
            <div>
              <input
                // ref={ageRef}
                value={foyer.conjointDernierContratTravail}
                type="date"
                autoFocus
                max={today}
                onChange={(e) => {
                  e.target.value = normalizeDateNumber(e.target.value);
                  setFoyer({
                    ...foyer,
                    conjointDernierContratTravail: e.target.value,
                  });
                }}
              />
              <span></span>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointDernierContratTravail}
              style={{
                backgroundColor:
                  foyer.conjointDernierContratTravail && "#2e74b9",
              }}
              onClick={() => {
                setSection("mon logement");
                setProgress(60);
                setData((data) => {
                  return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                });
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </>
      )}

      {page === 6.8 && (
        <div className="simulateur-nationalite">
          <span>Son taux de handicap est de :</span>
          <div>
            <div
              className="conjointTauxHandicap"
              onClick={(e) => singleSelect(e, ".conjointTauxHandicap")}
              id="conjointTauxHandicap"
            >
              Moins de 50%
            </div>
            <div
              className="conjointTauxHandicap"
              onClick={(e) => singleSelect(e, ".conjointTauxHandicap")}
              id="conjointTauxHandicap"
            >
              Entre 50% et 80%
            </div>
            <div
              className="conjointTauxHandicap"
              onClick={(e) => singleSelect(e, ".conjointTauxHandicap")}
              id="conjointTauxHandicap"
            >
              Plus de 80%
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointTauxHandicap}
              style={{
                backgroundColor: foyer.conjointTauxHandicap && "#2e74b9",
              }}
              onClick={() => {
                if (
                  foyer.conjointStatutPersonnel2.includes("Inapte au travail")
                )
                  return setPage(6.9);

                setSection("mon logement");
                setProgress(60);
                setData((data) => {
                  return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                });
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}

      {page === 6.9 && (
        <div className="simulateur-nationalite">
          <span>Il/elle bénéficie d’une pension d’invalidité</span>
          <div>
            <div
              className="conjointPensionInvalidite"
              onClick={(e) => singleSelect(e, ".conjointPensionInvalidite")}
              id="conjointPensionInvalidite"
            >
              Oui
            </div>
            <div
              className="conjointPensionInvalidite"
              onClick={(e) => singleSelect(e, ".conjointPensionInvalidite")}
              id="conjointPensionInvalidite"
            >
              Non
            </div>
          </div>
          <div className="button-simulateur">
            <button
              style={{ backgroundColor: "#2e74b9" }}
              onClick={() => setPage(6)}
            >
              <i className="fas fa-chevron-left"></i>
              <span>Précédent</span>
            </button>
            <button
              disabled={!foyer.conjointPensionInvalidite}
              style={{
                backgroundColor: foyer.conjointPensionInvalidite && "#2e74b9",
              }}
              onClick={() => {
                setSection("mon logement");
                setProgress(60);
                setData((data) => {
                  return { ...data, ...foyer,conjointStatutPersonnel:[foyer.conjointStatutPersonnel, ...foyer.conjointStatutPersonnel2].filter((el) => el !== "") };
                });
              }}
            >
              <span>Suivant</span>
              <i className="fas fa-chevron-right"></i>
            </button>
          </div>
        </div>
      )}
    </>
  );
}

export default Foyer;
