import { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import '../Assets/css/faq.css'
import { faqs } from '../Raw/faqs'
import { toggleMenuAndScrollBar } from '../Tools/animations'



const Faq = () => {
    const { qType, qId } = useParams();
    useEffect(() => {
        const faqExist = document.getElementById('faq-' + qType + '-' + qId)
        if (qType && qId && faqExist) {
            faqExist.classList.toggle('isDropDownVisibleByHeightAnimation')
            if (document.querySelector('main').classList.contains('mainMoveRight')) {
                toggleMenuAndScrollBar()
            }
            window.scrollTo({ top: faqExist.offsetTop - 150, behavior: 'smooth' });
        } else {
            console.log('oh')
        }

    }, [qType, qId])


    function showResponse(questionId, questionType) {
        document.querySelector('#faq-' + questionType + '-' + questionId)
            .classList.toggle('isDropDownVisibleByHeightAnimation')
    }



    return <div className="faq">
        <h2>Foire aux questions
            <br />Pour répondre a toutes vos preoccupations ou pour vous guider sur notre plateforme</h2>
        <section>
            <h4>Questions générales</h4>
            {
                faqs.géneral.map((q, index) => <div key={"faq general nb " + index}
                    className={"faq-géneral" + (index + 1)}>
                    <b onClick={() => showResponse(index + 1, 'géneral')}>
                        <i className='mdi mdi-arrow-right-circle-outline'></i>
                        {q.question} </b>
                    <p id={'faq-géneral-' + (index + 1)}>{q.response} </p>
                </div>)
            }
        </section>

        <section>
            <h4>Questions sur le tarif</h4>
            {
                faqs.tarifs.map((q, index) => <div key={"faq tarif nb " + index}>
                    <b onClick={() => showResponse(index + 1, 'tarifs')}>
                        <i className='mdi mdi-arrow-right-circle-outline'></i>
                        {q.question} </b>
                    <p id={'faq-tarifs-' + (index + 1)}>{q.response} </p>
                </div>)
            }
        </section>
    </div>
}

export default Faq;