import axios from "axios";
import { useState } from "react";
import { useAuth } from "../Hooks/authHooks";
import { allFields } from "../Raw/fields";
import { handleUserFieldInfoChange } from "../Tools/formFunction";
import { dispatchBtn, dispatchInputOrSelect } from "../Tools/formValidator";
import uniqId from 'uniqid';
export const FormBtn = ({ btn }) => {
    return <div className="formBtn">
        {btn}
    </div>
}

export const UpdateProfilForm = ({ props }) => {
    const { apiInfos, notify } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { authedInfo, formNb } = props;

    const ufFields = {
        "form1": [allFields.adresse,
        allFields.codepostal, allFields.password,
        allFields.password_confirmation],
        "form2": [allFields.nom, allFields.prenom,
        allFields.date_de_naissance],
        "form3": [allFields.mail, allFields.telephone]
    };


    const [formValues, setFormValues] = useState({
        nom: authedInfo.nom, prenom: authedInfo.prenom,
        date_de_naissance: authedInfo.date_naissance,
        codepostal: authedInfo.codepostal, adresse: authedInfo.address,
        email: authedInfo.email, telephone: authedInfo.telephone,
        currentPassword: '', password: '', password_confirmation: "",
        avatar: "",

        situation_familiale: authedInfo.situation_familiale,
        nom_conjoint: authedInfo.nom_conjoint, prenom_conjoint: authedInfo.prenom_conjoint,
        date_de_naissance_conjoint: authedInfo.date_naissance_conjoint
    })

    function handleSubmit(event) {
        event.preventDefault();
        let subApiUrl = '';
        let formData = '';
        setFormBtn(dispatchBtn('disableAndLoad', 'Sauvegarde en cours...'))
        if (formNb == "1") {
            subApiUrl = '/user/update-profil'
            formData = new FormData();
            formData.append("info", JSON.stringify({
                "mainPassword": "",
                "address": formValues.adresse,
                "avatar": {},
                "newPassword": formValues.password,
                "codepostal": formValues.codepostal
            }))
            formData.append("avatar", formValues.avatar)
        } else if (formNb == "2") {
            subApiUrl = "/user/update-situation-familiale"
            formData = {
                "date_naissance": formValues.date_de_naissance,
                "date_naissance_conjoint": formValues.date_de_naissance_conjoint,
                "nom": formValues.nom,
                "nom_conjoint": formValues.nom_conjoint,
                "prenom": formValues.prenom,
                "prenom_conjoint": formValues.prenom_conjoint,
                "situation_familiale": formValues.situation_familiale
            }
        } else if (formNb == "3") {
            // console.log('email')
            subApiUrl = '/user/update-phone'
            formData = {
                "email": formValues.email,
                "telephone": formValues.telephone
            }
        } else if (formNb == "4") {

        }

        axios.put(baseApi + subApiUrl, formData, { headers: headerApi })
            .then(res => {
                res.data.nom ? notify('success', 'Modification enregistré avec success')
                    : notify('error', 'Une erreur est survenue ,veuillez reesayer')
                setFormBtn(dispatchBtn('simple', 'Sauvegarder'))

            })
            .catch(err => {
                notify('error', 'Une erreur est survenue ,veuillez reesayer')
                setFormBtn(dispatchBtn('simple', 'Sauvegarder'))
            })
    }

    function handleChange(event) {
        handleUserFieldInfoChange(event, formValues, setFormValues, errors, setErrors)
        console.log(event.target.name)
    }


    // const [formNb, setFormNb] = useState()
    const [formBtn, setFormBtn] = useState(dispatchBtn('simple', 'Sauvegarder'))
    const [errors, setErrors] = useState([])

    return <> <div>
        <form onSubmit={(event) => handleSubmit(event)}>
            <h3>{props.formTitle} </h3>
            {
                ufFields["form" + props.formNb].map((field, index) => <div key={"update form" + index}
                    className="formSegment fsFull">
                    <label>
                        {field.label}
                    </label>
                    <section>
                        {dispatchInputOrSelect(formValues, setFormValues, field,
                            handleChange, errors, setErrors)}
                    </section>
                </div>)
            }
            {
                props.formNb == "2" ? <Form2Completion props={{
                    authedInfo,
                    formValues, setFormValues, handleChange,
                    errors, setErrors
                }} />
                    : ''
            }
            <FormBtn btn={formBtn} />
        </form>
    </div>
        {
            formNb == "2" && formValues.situation_familiale === "marié_avec_enfant" ?
                <AddChildren props={{ authedInfo }} /> : ''
        }
    </>
}



const Form2Completion = ({ props }) => {
    const situationFamilialeField = [
        { value: "célibataire", ph: "Célibataire" },
        { value: "marié_avec_enfant", ph: "Marié avec enfant" },
        { value: "marié_sans_enfant", ph: "Marié sans enfant" },
    ]

    const conjointfields = [
        allFields.nom_conjoint, allFields.prenom_conjoint,
        allFields.date_de_naissance_conjoint
    ]
    const { authedInfo, formValues, setFormValues, handleChange, errors, setErrors } = props;
    return <>
        <div className="formSegment fsFull">
            <label>Situation Familiales</label>
            <section>
                {
                    situationFamilialeField.map((ip, idp) => <label
                        key={"radio nb" + idp}
                        htmlFor={"sf_radio_nb" + idp}>

                        <input type="radio" name="situation_familiale"
                            value={ip.value}
                            id={"sf_radio_nb" + idp}
                            defaultChecked={ip.value === authedInfo.situation_familiale}
                            onChange={(event) => handleChange(event)}
                            errmsgname="noErrWithRadio"
                        />
                        <span>{ip.ph}</span>

                    </label>)
                }
            </section>
        </div>
        {
            ['marié_avec_enfant', 'marié_sans_enfant'].
                includes(formValues.situation_familiale) ?
                <>
                    {
                        conjointfields.map((cf, index) => <div className="formSegment fsFull"
                            key={"conjoint field" + index}>
                            <label>{cf.label} </label>
                            <section>
                                {dispatchInputOrSelect(formValues, setFormValues, cf,
                                    handleChange, errors, setErrors)}
                            </section>
                        </div>)
                    }
                </>
                : ''
        }

    </>
}


const AddChildren = ({ props }) => {
    const enfantFields = [allFields.nom_enfant, allFields.prenom_enfant,
    allFields.date_de_naissance_enfant]
    const { apiInfos, notify } = useAuth();
    const { baseApi, headerApi } = apiInfos;
    const { authedInfo } = props;


    const [currentField, setCurrentField] = useState(false);
    const [childList, setChildList] = useState(authedInfo.enfants)
    const [cFormValues, setCFormValues] = useState({
        nom_enfant: "", prenom_enfant: "",
        date_de_naissance_enfant: ""
    })

    function handleC(e) {
        const name = e.target.name;
        const value = e.target.value;
        // console.log(name)
        setCFormValues({ ...cFormValues, [name]: value })
    }

    function handleNewChildField(e) {
        e.preventDefault()
        setCurrentField(true)
    }

    function addNewChild(e) {
        e.preventDefault();
        let childCopy = childList;

        if (childCopy.length > 0) {
            childCopy.push({
                id: uniqId(),
                nom: cFormValues.nom_enfant,
                prenom: cFormValues.prenom_enfant,
                date_naissance: cFormValues.date_de_naissance_enfant
            })
        } else {
            childCopy = [
                {
                    id: uniqId(),
                    nom: cFormValues.nom_enfant,
                    prenom: cFormValues.prenom_enfant,
                    date_naissance: cFormValues.date_de_naissance_enfant
                }
            ]
        }
        setCurrentField(false)
        // setChildList(childCopy)

        axios.put(baseApi + '/user/update-enfants', { enfants: childCopy }, { headers: headerApi })
            .then(res => {
                notify('success', 'Enfant enregistrés avec success')
                setChildList(childCopy)
            })
            .catch(err => notify('error', "Une erreur est survenue pendant l'ajout de l'enfant"))

    }

    function ret() {
        if (currentField) {
            return (<>
                {enfantFields.map((ef, index) => <div className="formSegment fsFull"
                    key={"add child field" + index}>
                    <label>{ef.label} </label>
                    <section>
                        <input type={ef.htmlType} placeholder={ef.ph} name={ef.name}
                            onChange={(event) => handleC(event)} />
                    </section>
                </div>)}
                {/* <FormBtn btn={dispatchBtn('simple', 'Sauvegarder cet enfant')} /> */}
                <div className="formBtn">
                    <button type="action"
                        onClick={(event) => addNewChild(event)}>
                        Sauvegarder cet enfant</button>
                </div>
            </>)
        } else {
            return ''
        }
    }

    return <div>
        <form>
            <h3>Consulter/Ajouter/Modifier la liste de vos enfants</h3>
            <section className="upProfilChildList">
                {
                    childList.length > 0 ?
                        childList.map((ch, index) => <div key={"client child nb" + index}>
                            <p>
                                <b>Nom: </b>
                                <span>{ch.nom}</span>
                            </p>
                            <p>
                                <b>Prénom: </b>
                                <span>{ch.prenom}</span>
                            </p>
                            <p>
                                <b>Naissance: </b>
                                <span>{ch.date_naissance}</span>
                            </p>
                            <p>
                                <button>Supprimer cet enfant</button>
                            </p>
                        </div>)
                        : ''
                }
            </section>
            {
                !currentField ?
                    <div className="childAdderBtn">
                        <button type="action" onClick={(event) => handleNewChildField(event)}>
                            Ajouter un nouvel enfant</button>
                    </div> : ''
            }
            {ret(currentField)}
        </form>
    </div>
}







export const Form4 = ({ props }) => {
    const { authedInfo } = props;
    const { apiInfos, notify } = useAuth();
    const { baseApi, headerApi } = apiInfos;

    const radioArray = [
        { label: 'Étudiant(e)', value: 'etudiant' },
        { label: 'Apprenti(e)', value: 'apprenti' },
        { label: 'Retraité(e)', value: 'retraite' },
        { label: 'Demandeur d’emploi', value: 'demandeur_emploi' },
        { label: 'Enceinte', value: 'enceinte' },
        { label: 'En situation de handicap', value: 'en_situation_de_handicap' },
        { label: 'Inapte au travail', value: 'inapte_au_travail' },
        { label: 'Actif (salarié / indépendant)', value: 'actif' },
    ]

    const [formValues, setFormValues] = useState({
        actif: authedInfo.situation_professionnelle.actif,
        apprenti: authedInfo.situation_professionnelle.apprenti,
        demandeur_emploi: authedInfo.situation_professionnelle.demandeur_emploi,
        en_situation_de_handicap: authedInfo.situation_professionnelle.en_situation_de_handicap,
        enceinte: authedInfo.situation_professionnelle.enceinte,
        etudiant: authedInfo.situation_professionnelle.etudiant,
        inapte_au_travail: authedInfo.situation_professionnelle.inapte_au_travail,
        retraite: authedInfo.situation_professionnelle.retraite
    })

    const [formValuesC, setFormValuesC] = useState({
        actif: authedInfo.situation_professionnelle_conjoint.actif,
        apprenti: authedInfo.situation_professionnelle_conjoint.apprenti,
        demandeur_emploi: authedInfo.situation_professionnelle_conjoint.demandeur_emploi,
        en_situation_de_handicap: authedInfo.situation_professionnelle_conjoint.en_situation_de_handicap,
        enceinte: authedInfo.situation_professionnelle_conjoint.enceinte,
        etudiant: authedInfo.situation_professionnelle_conjoint.etudiant,
        inapte_au_travail: authedInfo.situation_professionnelle_conjoint.inapte_au_travail,
        retraite: authedInfo.situation_professionnelle_conjoint.retraite
    })



    function dispatchRadio(conjoint = null) {
        let formValueList = conjoint != null ? formValuesC : formValues;
        let filtered = [];

        if (formValueList.etudiant == "true") {
            filtered = radioArray.filter(ra => !['actif', 'apprenti', 'retraite'].includes(ra.value))
        }
        else if (formValueList.apprenti == "true") {
            filtered = radioArray.filter(ra => !['actif', 'etudiant', 'retraite'].includes(ra.value))
        }
        else if (formValueList.retraite == "true") {
            filtered = radioArray.filter(ra => !['actif', 'apprenti', 'etudiant'].includes(ra.value))
        }
        else {
            filtered = radioArray
        }
        return filtered;
    }

    function parseToBoolean(char) {
        if (char == "true") {
            return true
        } return false;
    }

    function handleSubmit(e) {
        e.preventDefault();
        let formData = {
            situationProfessionnelle: formValues,
            situationProfessionnelleConjoint: formValuesC
        };

        axios.put(baseApi + "/user/update-situation-professionnelle", formData,
            { headers: headerApi })
            .then(res => notify('success', 'Situations professionnelles modifiés avec success'))
            .catch(err => notify('error', 'Une erreur est survenue'))

    }

    function handleCheck(event, value, conjoint = null) {

        if (conjoint != null) {
            setFormValuesC({ ...formValuesC, [value]: String(event.target.checked) })
        } else {
            setFormValues({ ...formValues, [value]: String(event.target.checked) })
        }
    }


    return <div>
        <form className="form4" onSubmit={event => handleSubmit(event)}>
            <h3>Modifier votre situation professionelle ou autre situation </h3>
            <div className="formSegment" id="checkBoxSegment">
                <b>Je suis ....</b>
                {
                    dispatchRadio().map((ip, index) => <label key={'label input nb' + index}
                        htmlFor={"situation" + index}
                        className="checkboxSelect">
                        {
                            < input type="checkbox" name="situation"
                                id={"situation" + index} value={ip.value}
                                defaultChecked={parseToBoolean(formValues[ip.value])}
                                onChange={event => handleCheck(event, ip.value)}
                                key={new Date()} />
                        }
                        <span> {ip.label}</span>

                    </label>)
                }
            </div>

            {
                authedInfo.situation_familiale != "célibataire" ?
                    <div className="formSegment" id="checkBoxSegment">
                        <b>Mon/Ma conjoint(e) est </b>
                        {
                            dispatchRadio('conjoint').
                                map((ip, index) => <label key={'label conj input nb' + index}
                                    htmlFor={"situation_conjoin" + index}
                                    className="checkboxSelect">
                                    {
                                        < input type="checkbox" name="situation_conjoin"
                                            id={"situation_conjoin" + index} value={ip.value}
                                            defaultChecked={parseToBoolean(formValuesC[ip.value])}
                                            onChange={event => handleCheck(event, ip.value, 'conjoint')}
                                            key={'conjoin_' + new Date()} />
                                    }
                                    <span> {ip.label}</span>

                                </label>)
                        }
                    </div> : ''
            }
            <FormBtn btn={dispatchBtn('simple', 'Sauvegarder')} />
        </form>
    </div>
}