import { useState } from "react";
import { Fade, } from "react-reveal";
import { Link } from "react-router-dom"
import Slider from "react-slick";
import { SimulationLink } from "../GlobalComponents/Btn";
import { IllustrationImage, SimpleImage } from '../GlobalComponents/img/global';

export const Banner = () => {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };

    return <div className="hBanner">
        <section className='hb-text'>
            <div>
                <Fade cascade>
                    <h1>
                        J'ai des droits sociaux !
                    </h1>
                    <b>
                        Faute d'informations ou en raison d'une complexité des démarches,<br />
                        de nombreuses prestations sociales
                        ne sont pas réclamées<br /> par les personnes qui y ont pourtant droit !
                    </b>

                </Fade>
                <section>
                    <Link to="/Inscription">S'inscrire</Link>
                    <SimulationLink />
                </section>
            </div>
        </section>

    </div>
}


export const HowItWord = () => {
    const hiws = [
        {
            title1: "En fonction de votre âge , statut ou revenus " +
                "vous pouvez estimer vos prestations sociales et voir quelles aides vous correspondent",
            title2: 'Afin de réclamer ces prestations ,France Assist vous accompagne',
            img: 'calculatorb.svg', align: 'right'
        },
        {
            title1: 'Un conseiller attitré vous accompagne pas à pas',
            title2: "Tout est fait pour vous faire gagner du temps et avoir l'esprit tranquille.",
            img: 'expertb.svg', align: 'left'
        },
        {
            title1: "Réclamez et recevez vos aides de la part de l'Etat",
            title2: "Après analyse de votre simulation, nous entamons la réclamation de vos aides auprès des différents organismes concernés.",
            img: 'getMoneyb.svg', align: 'right'
        }
    ]

    const dispastchAnim = (key, align, children) => {
        return align == 'left' ?
            <Fade bottom key={key} duration={1000}>
                {children}
            </Fade> :
            <Fade bottom key={key} duration={1000}>
                {children}
            </Fade>
    }
    return <div className='hHowItWord'>
        <Fade>
            <h2>Faite votre estimation</h2>
        </Fade>
        <SimulationLink />

        {hiws.map((hiw, index) => dispastchAnim('hiw nb' + index, hiw.align, <section
            className={hiw.align == 'left' ? 'leftTxtection' : 'rightTxtection'} >
            <span>{index + 1} </span>
            <div>
                <h3>{hiw.title1}</h3>
                <p>{hiw.title2}</p>
            </div>
            <div>
                <IllustrationImage props={{ src: 'howItWork/' + hiw.img, alt: 'hiw 1' }} />
            </div>
        </section>))}
    </div>
}


