import '../Assets/css/souscription.css'
import { useParams } from "react-router-dom"
import { useState } from 'react';
import { useAuth } from '../Hooks/authHooks'
import { ResumeDeLaCommande } from "../GlobalComponents/OtherOnWebsite"
import { allFields } from '../Raw/fields';
import { handleUserFieldInfoChange } from '../Tools/formFunction';
import { dispatchBtn } from '../Tools/formValidator';
import { SubscriptionForm } from '../GlobalComponents/OtherOnWebsite';
import axios from 'axios';

import { useStripe } from "@stripe/react-stripe-js";
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';

const stripePromise = loadStripe('pk_live_51Jc7k0DA83aSRojOcslcDv24eNXj3cOEx9doSf8oEgyAMg7LhjstuBYIT2WQSO2RaGrjYZpWcm61iT8fs03HqfXY00sIo4gz7d');



const Souscription = () => {
    const { typeCommande } = useParams();
    const { apiInfos, notify } = useAuth();
    const { headerApi, baseApi } = apiInfos;

    const stripe = useStripe()

    const subscriptionFields = [
        allFields.nom, allFields.prenom,
        allFields.mail,
        allFields.codepostal, allFields.telephone,
        allFields.codecarte,
        allFields.mois_expiration_carte, allFields.année_expiration_carte,
        allFields.codederrierecarte,
    ]

    const [errors, setErrors] = useState([]);
    const btnContent = <span><i className="mdi mdi-cash-lock"></i> Valider</span>
    const [formBtn, setFormBtn] = useState(dispatchBtn('simple', btnContent))

    const [formValues, setFormValues] = useState({
        nom: "", prenom: "",
        codepostal: "", telephone: "", email: '',
        carte_expiration_mois: "", carte_expiration_année: "",
        number: "", code_derriere_carte: "",
    });

    function handleChange(event) {
        const name = event.target.name;
        const value = event.target.value;
        handleUserFieldInfoChange(event, formValues, setFormValues, errors, setErrors)

    }

    function checkPaiement(apiResponse) {
        // const { latest_invoice } = apiResponse.data.results[0];
        // const { payment_intent } = latest_invoice;
        // const { client_secret, status } = payment_intent;

        const { client_secret, status } = apiResponse.data.results;
        if (status === "requires_action" || status === "requires_payment_method"
        ) {
            stripe.confirmCardPayment(client_secret)
                .then(scRes => {
                    if (scRes.error) {
                        notify('error', 'Une erreur est survenue:' + scRes.error.message)
                    } else {
                        notify('success', 'Paiement effectué avec success')
                    }
                })
                .catch(scErr => notify('error', 'Erreur de confimation de carte :'
                    + scErr.error.message))
        } else {
            notify('success', 'Paiement effectué avec success')
        }



    }

    function handleSubmit(event) {
        setFormBtn(dispatchBtn('disableAndLoad', 'En cours....'))
        event.preventDefault();
        let body = {
            "nom": formValues.nom, "prenom": formValues.prenom,
            "telephone": formValues.telephone, "email": formValues.email,
            "codepostal": formValues.codepostal,
            "number": formValues.number,
            "exp_month": formValues.carte_expiration_mois,
            "exp_year": formValues.carte_expiration_année,
            "cvc": formValues.code_derriere_carte, "password": formValues.password
        };

        axios.post(baseApi + '/user/charge', body, { Headers: headerApi })
            .then(res => {
                checkPaiement(res)
                setFormBtn('simple', btnContent)
            })
            .catch(err => console.log(err))

    }


    return <div className="souscription">
        <h2>France Assist souscription (offre a la tache) </h2>

        <div>
            <section className='sForm'>

                <SubscriptionForm props={{
                    formFields: subscriptionFields,
                    handleSubmit, handleChange,
                    formValues, setFormValues,
                    errors, setErrors,
                    formBtn,
                    subscriptionPrice: '29,90'
                }} />

            </section>
            <ResumeDeLaCommande props={{ typeCommande }} />
        </div>
    </div>
}


const StripeWraper = () => {
    return <Elements stripe={stripePromise}>
        <Souscription />
    </Elements>
}
export default StripeWraper;