import { useState } from "react"
import { Fade } from "react-reveal";
import { Link } from "react-router-dom";
import { showMenuAndMoveMainAnimation } from "../../Tools/animations";
import { AuthLinks } from "./navbar";

export const GeneralSideBar = ({ props }) => {
    const { subMenuKey, menuObject, authed } = props
    const [menuLinksList, setmenuLinksList] = useState(menuObject);
    const [cNb, setCNB] = useState(0);
    const [bigCategoryId, setBigCategoryId] = useState(0);


    function handleSubCategory(event, index, nextKey) {
        const currentC = menuLinksList[index];
        if (currentC[nextKey]) {
            setmenuLinksList(currentC[nextKey])
            setCNB(cNb + 1)
        }

        if (currentC[nextKey][0][nextKey]) {
            setBigCategoryId(index)
        }
    }

    function handlePreviousCategory(event, nextKey) {
        setCNB(cNb - 1)
        if (cNb == 1) {
            setmenuLinksList(menuObject)
        } else if (cNb == 2) {
            setmenuLinksList(menuObject[bigCategoryId][nextKey])
        }

    }
    return <aside className="navSidebar">
        <section>
            <p onClick={() => { showMenuAndMoveMainAnimation() }}>
                Fermer <i className="mdi mdi-close"></i>
            </p>
        </section>
        <Fade right key={'category list' + cNb} duration={400}>
            {
                menuLinksList.length != menuObject.length ?
                    <section onClick={(event) => handlePreviousCategory(event, subMenuKey)}>
                        <i className="mdi mdi-chevron-left"></i>
                        <span>Revenir en arriere</span>
                    </section>
                    : ""
            }
            {
                menuLinksList.map((ct, index) => {
                    return ct[subMenuKey] && ct[subMenuKey].length > 0 ? <section key={'sidebar category nb' + index}
                        onClick={(event) => handleSubCategory(event, index, subMenuKey)}>
                        <span>{ct.label}</span>
                        <i className="mdi mdi-chevron-right"></i>
                    </section>
                        : <Link to={ct.link} key={'sidebar category nb' + index}>
                            <span>{ct.label} </span>
                            <i className="mdi mdi-chevron-right"></i>
                        </Link>
                })
            }
            <section>
                <AuthLinks props={{ authed }} />
            </section>
        </Fade>

    </aside >
}