import {GET_USER, LOADINGADMIN} from "../actions/types";

  const initialState = {
    users:[],
    loading:true
  };
  export default function users(state = initialState, action) {
    switch (action.type) {
      case GET_USER:
        return {
          ...state,
         users:action.payload
        };
        case LOADINGADMIN:
          return {
            ...state,
           loading:action.payload
          };
      default:
        return state;
    }
  }