import { isValidLenght, isValidChar, isValidAgeValue, isValidNumberValue } from "./formValidator";
import cities from "../zip-codes/cities.json"
import moment from "moment";
import { normalizeCardNumber, normalizeName, normalizeSimpleNumber } from "./Forms/normaliser";

function objectKeyExistOnArray(array, targetKey, targetValue) {
    const searchRes = array.findIndex(el => el[targetKey] === targetValue);
    return searchRes
}
function checkPostalCode(name, codeList, codeValue, errors, setErrors) {
    let test = false;
    for (var i = 0; i < codeList.length; i++) {
        if (codeList[i].zip_code === codeValue) {
            test = true;
            break;
        }
    }

    if (test) {
        const copy = errors
        if (objectKeyExistOnArray(errors, 'type', 'charPostalCode') != -1) {
            copy.splice(objectKeyExistOnArray(errors, 'type', 'charPostalCode'), 1)
            setErrors(copy)
        }

    } else {
        if (objectKeyExistOnArray(errors, 'type', 'charPostalCode') == -1) {
            setErrors([...errors, { name, error: 'Code postal erroné', type: 'charPostalCode' }])
        }
    }
}

function checkCurrentYear(name, year, month, errors, setErrors) {
    if (year >= parseInt(moment().format('YYYY'))
        && objectKeyExistOnArray(errors, 'type', 'charExpYear') != -1) {
        const copy = errors
        copy.splice(objectKeyExistOnArray(errors, 'type', 'charExpYear'), 1)
        setErrors(copy)

    } else {
        if (objectKeyExistOnArray(errors, 'type', 'charExpYear') < 0) {
            setErrors([...errors, { name, error: "Mois ou Année d'expiration incorrect", type: 'charExpYear' }])
        }
    }
}

export const handleUserFieldInfoChange = (event, userInfo, setUserInfo, errors, setErrors) => {
    const name = event.target.name;
    var value = event.target.value;
    const errMsgName = event.target.attributes['errmsgname'].value;

    // isValidLenght(errors, setErrors, name, value, 2, 25, 'Votre ' + errMsgName + ' ')
    if (['autre_competence', 'localisation_entreprise', 'description_entreprise']
        .includes(name)) {
        isValidLenght(errors, setErrors, name, value, 10, 500, 'Votre ' + errMsgName + ' ')
    }
    if (name === 'email') {
        isValidChar('isMail', errors, setErrors, name, value, ' ')
    }
    if (['nom', 'prenom'].includes(name)) {
        // isValidChar('isAlpha', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
        value = normalizeName(value)
    }
    if (['date_de_naissance',].includes(name)) {
        isValidAgeValue(errors, setErrors, name, value, 16, 50, 'Votre age  ')
    }
    if (name == 'codepostal') {
        checkPostalCode(name, cities, value, errors, setErrors)
    }
    if (name == 'number') {
        // isValidChar('isNumeric', errors, setErrors, name, value, 'Votre ' + errMsgName + ' ')
        const inp = document.getElementsByName(name)[0]
        inp.setAttribute('maxlength', 19)
        value = normalizeCardNumber(value)
    }
    if (name == 'carte_expiration_année') {
        checkCurrentYear(name, value, userInfo['carte_expiration_mois'], errors, setErrors)
    }
    if (['codepostal', 'telephone', 'code_derriere_carte',
        'carte_expiration_mois', 'carte_expiration_année'].includes(name)) {
        let maxLength = 0;
        if (name == 'codepostal') {
            maxLength = 5
        } else if (name == 'telephone') {
            maxLength = 10
        } else if (name == 'code_derriere_carte') {
            maxLength = 3
        } else if (name == 'carte_expiration_mois') {
            maxLength = 2
        } else if (name == 'carte_expiration_année') {
            maxLength = 4
        }
        value = normalizeSimpleNumber(value, maxLength)
    }
    if (['logo_entreprise', 'photo_1', 'photo_2', 'profil', 'image'].includes(name)) {
        var maxfilesize = 2048 * 1024  // 2 Mb;
        let file = event.target.files[0];
        var filesize = event.target.files[0].size;
        if (file && filesize <= maxfilesize) {
            let imgUrl = URL.createObjectURL(file)
            setTimeout(() => {
                document.getElementById(name + '-preview').src = imgUrl
            }, 500)

            value = file
        } else {
            const errCopy = errors;
            errors.push({ name: name, error: 'Le fichier ne peut exceder  2mb/mo', type: 'isFileValid' })
            setErrors([...errors, errCopy]);
        }

    }

    if (name === 'password') {
        // isValidChar('isPassword', errors, setErrors, name, value, 'Votre mot de passe ')
        isValidLenght(errors, setErrors, name, value, 6, 50, 'Votre mot de passe ')
    }
    if (name === 'password_confirmation') {
        isValidChar('isPasswordConfirm', errors, setErrors, name, value,
            'Les mots de passe ', userInfo.password)
    }
    setUserInfo({ ...userInfo, [name]: value })
}

export const dispatchFormData = (formData, targetObject, formValues) => {
    targetObject.map(data => {
        if (['image_url'].includes((data.key))) {
            const key = data.key.split('_');
            formData.append(key[0], formValues[data.nameOnForm])
        } else {
            formData.append(data.key, formValues[data.nameOnForm])
        }
    })
}